import Vimeo from '@u-wave/react-vimeo';
import "./Video.scss";
export default function Video(props) {
  const { video_id } = props;
  return (
    <div className="video-container">
      <div className="video-player_one">
        <Vimeo
          video={video_id}
          autoplay
          muted
          loop
          responsive
          controls
        />
      </div>
    </div>
  );
}
