const Select = ({ label, labelTitle, required, options }) => {
    return (
        <div className="custom__form__group">
            <label htmlFor={label} className="custom__form__label  ">
                {labelTitle} {/* {required && <span className="text-danger require ">*</span>} */}
            </label>
            <select id={label} class="custom__form__input form-select " aria-label="Default select example">
                <option value=""> --Select-- </option>
                {options && options.map((option) => (
                    <option key={option.id} value={option.value}> {option.label}</option>
                ))}
            </select>
        </div>
    )
}

export default Select