import React from 'react';
import ReactOwlCarousel from 'react-owl-carousel';
import { option } from '../../Event/Option';
import Profile from "../../Profile/Profile";


const HireArtists = ({ artists }) => {

    return (
        <div className="container navs-right event-wrapper">
            {artists &&
                <ReactOwlCarousel className="owl-theme" {...option}>
                    {artists.map((artists) => (
                        <Profile key={artists.artist_id} artists={artists} />
                    ))}
                </ReactOwlCarousel>
            }
        </div>
    );
};

export default HireArtists;