import { IoMdNotificationsOutline } from 'react-icons/io';

const Payment = () => {
    return (
        <section className="privacy__policy">
            <h3>Payment systems</h3>
            <p>
                We offer multiple ways for you to take payment from your
                customers for their orders. You must have a minimum of one set
                up in order to publish a paid event and charge for tickets.
                <a href="#">Learn more about processing card payments.</a>
            </p>
            <div className="checkout__form__box">
                <div className="checkout__form__header">
                    <h5>Stripe (recommended)</h5>
                    <div className="checkout__action__btn">
                        <button type="button" className="action__btn">
                            Connect a Stripe account
                        </button>
                    </div>
                </div>

                <div className="notification">
                    <div className="notification__icon">
                        <IoMdNotificationsOutline />
                    </div>
                    <div className="notification__content">
                        <p>
                            Easy to set up, great support and a simple purchase
                            experience for your customers to pay by card. Stripe
                            has low fees and typically pays out to your bank
                            account 7 days after the money was paid to you.
                        </p>
                        <a href="link__here">Learn More</a>
                    </div>
                </div>
            </div>

            <div className="checkout__form__box mt-4">
                <div className="checkout__form__header">
                    <h5>PayPal</h5>
                    <div className="checkout__action__btn">
                        <button type="button" className="action__btn">
                            Connect a PayPal account
                        </button>
                    </div>
                </div>

                <div className="notification">
                    <div className="notification__icon">
                        <IoMdNotificationsOutline />
                    </div>
                    <div className="notification__content">
                        <p>
                            Gives your customers the option to pay via PayPal or
                            card. A convenient option if you already have a
                            PayPal account set up.
                        </p>
                        <a href="link__here">Learn More</a>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Payment;
