import {
    FaFacebookF, FaInstagram,
    FaTwitter,
    FaYoutube
} from "react-icons/fa";
import { Link } from 'react-router-dom';
import './Title.scss';

const Title = ({ title, link, titleNumber }) => {
    return (
        <div className="container">
            <div className="title">
                <h5 className="title__name"> {title} </h5>
                <hr className="title__line" style={{ margin: `${titleNumber === 1 && ' 0 95px 0 25px'}` }} />
                <div className="title__right">
                    {titleNumber === 1 && (
                        <>
                            <Link to={`${link || '/'}`} className="title__seeAll">
                                See All
                            </Link>
                        </>
                    )}
                    {titleNumber === 2 && (
                        <div className="title__buttons-2">
                            <Link className="title__btn" to={'/ticket/' + link}>
                                Buy Ticket
                            </Link>
                            <Link className="title__btn" to={link || '/'}>
                                Donate
                            </Link>
                        </div>
                    )}
                    {titleNumber === 3 && (
                        <div className="title__buttons-2">

                        </div>
                    )}
                    {titleNumber === 4 && (
                        <div className="title__buttons-2">
                            <div className="footer__social__wrapper">
                                <Link to={{ pathname: "http://www.facebook.com/sharer/sharer.php?u" + window.location.href }} className="footer__social__icon" target="_blank">
                                    <FaFacebookF />
                                </Link>
                                <Link to="" className="footer__social__icon" target="_blank">
                                    <FaTwitter />
                                </Link>
                                <Link to="" className="footer__social__icon" target="_blank">
                                    <FaInstagram />
                                </Link>
                                <Link to="" className="footer__social__icon" target="_blank">
                                    <FaYoutube />
                                </Link>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Title;
