import {SLIDER_LIST_REQUEST,SLIDER_LIST_SUCCESS,SLIDER_LIST_FAIL} from '../constants/ticket_home_slider_constants';

export const sliderListReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case SLIDER_LIST_REQUEST:
            return {loading:true};

            case SLIDER_LIST_SUCCESS:
                return {loading:false, slider: action.payload};

                case SLIDER_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}