import {CREATE_EVENET_REQUEST,CREATE_EVENET_SUCCESS,CREATE_EVENET_FAIL,CREATE_EVENET_REQUEST_OFFLINE,CREATE_EVENET_SUCCESS_OFFLINE,CREATE_EVENET_FAIL_OFFLINE} from '../constants/create_event_constants';

import Axios from 'axios';
import instance from '../connection.js'

export const createEventActions = (event_name,
    start_date,
    end_date,
    genres,
    event_link,
    hear_about,
    email,
    contactNumber,
    platform,
    description,eventtime) => async(dispactch)=>{
    dispactch({
        type:CREATE_EVENET_REQUEST
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/create-event',{event_name,
            start_date,
            end_date,
            genres,
            event_link,
            hear_about,
            email,
            contactNumber,
            platform,
            description,eventtime});
        dispactch({type: CREATE_EVENET_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:CREATE_EVENET_FAIL,payload: error.message});
    }
}

export const createEventActionsoffline = (event,
    artistName,
    eventtime,
    startdate,
    endDate,
    address,
    artistBudget,
    email,
    contactNumber,
    description) => async(dispactch)=>{
    dispactch({
        type:CREATE_EVENET_REQUEST_OFFLINE
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/create-event-offline',{event,
            artistName,
            eventtime,
            startdate,
            endDate,
            address,
            artistBudget,
            email,
            contactNumber,
            description});
        dispactch({type: CREATE_EVENET_SUCCESS_OFFLINE,payload:data});
    }catch(error){
        dispactch({type:CREATE_EVENET_FAIL_OFFLINE,payload: error.message});
    }
}
