import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getHowtobuyTicket } from "../../../actions/How_to_host_your_show_actions";
import Title from "../../Shared/Title/Title";
import Video from "../../Video/Video";
import "./BuyTicket.css";

export default function HostShow(props) {
  const { info } = props;
  const dispatch = useDispatch();
  const getHowToShow = useSelector((state) => state.how_to_buy);
  const { loading, error, how_to_host } = getHowToShow;
  useEffect(() => {
    dispatch(getHowtobuyTicket());
  }, []);
  return (
    <div className="buyTicket add-margin">
      <Title title="HOW TO Buy Tickets" />
      <div className="container">
        {how_to_host ? <Video video={how_to_host} /> : <div></div>}
        <div className="buyTicket__content">
          {how_to_host ? (
            <div>
              {how_to_host.map((how_to_host) => (
                <div
                  key={how_to_host.how_id}
                  dangerouslySetInnerHTML={{ __html: how_to_host.how_text }}
                />
              ))}
            </div>
          ) : (
            <div></div>
          )}
        </div>
      </div>
    </div>
  );
}
