import {REGISTER_REQUEST,REGISTER_SUCCESS,REGISTER_FAIL,LOGIN_REQUEST, LOGIN_SUCCESS, LOGIN_FAIL} from '../constants/login_register_constants';

export const registerReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case REGISTER_REQUEST:
            return {loading:true};

            case REGISTER_SUCCESS:
                return {loading:false, register_data: action.payload};

                case REGISTER_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}

export const loginReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case LOGIN_REQUEST:
            return {loading:true};

            case LOGIN_SUCCESS:
                return {loading:false, login_data: action.payload};

                case LOGIN_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}