import Vimeo from '@u-wave/react-vimeo';
import "./Video.scss";

export default function Video(props) {
  const { video } = props;
  return (
    <div className="video-container">
      {video ? (
        <div className="video-player_one">
          {video.map((video) => (
            <Vimeo key={video.host_video}
              video={video.host_video}
              autoplay
              muted
              loop
              responsive
              controls
              className="video"
            />
          ))}
        </div>
      ) : (<div></div>)}
    </div>
  );
}
