import { FaHeart } from 'react-icons/fa';
import ReactImageAppear from 'react-image-appear';
import { Link } from 'react-router-dom';
import './Profile.scss';
import instance from '../../connection.js'

const Profile = (props) => {
    const { artists } = props;
    return (
        <div className="profile">
            <div className="profile__img">
                <center><ReactImageAppear src={instance+`/${artists.artist_image}`} alt={artists.artist_alt} /></center>
                <Link to={"/profile-details/" + artists.artist_slug} className="profile__link">
                    Visit Profile
                </Link>
            </div>

            <div className="profile__info">
                <h5 className="profile__name">{artists.artist_name}</h5>
                <h5 className="mt-1">{artists.artist_type}</h5>
                <h5 className="mt-1"> <FaHeart className="heart__icon" /> {artists.heart} </h5>
            </div>
        </div>
    );
};

export default Profile;
