import React from 'react';
import {
    FaFacebookF, FaInstagram,
    FaTwitter, FaWhatsapp, FaYoutube
} from 'react-icons/fa';

const Icons = () => {
    return (
        <div className="footer__social__wrapper">
            <a href="https://www.facebook.com/ChillHallApp" className="footer__social__icon">
                <FaFacebookF />
            </a>
            <a href="/" className="footer__social__icon">
                <FaTwitter />
            </a>
            <a href="/" className="footer__social__icon">
                <FaInstagram />
            </a>
            <a href="/" className="footer__social__icon">
                <FaYoutube />
            </a>
            <a href="/" className="footer__social__icon">
                <FaWhatsapp />
            </a>
        </div>
    );
};

export default Icons;