import { Link } from 'react-router-dom';
import './Search.scss';

const Search = () => {
    return (
        <div className="add-margin">
            <div className="container mt-4 pb-3">
                <form class="input-group ">
                    <input type="text" list="datalistOptions" class="form-control " placeholder="" aria-label="Example text with button addon" aria-describedby="searchBtn" />
                    <button class="btn btn-danger focus-none " type="submit" id="searchBtn">Search</button>
                </form>
                <datalist id="datalistOptions">
                    <option value="San Francisco" />
                    <option value="New York" />
                    <option value="Seattle" />
                    <option value="Los Angeles" />
                    <option value="Chicago" />
                </datalist>

                <div className="suggest">
                    <h6>Suggestions</h6>
                    <div className="suggest-list">
                        <Link to="/" className="suggest-link">
                            Banlga Movie
                        </Link>
                        <Link to="/" className="suggest-link">
                            English Movie
                        </Link>
                        <Link to="/" className="suggest-link">
                            Hindi Movie
                        </Link>
                        <Link to="/" className="suggest-link">
                            Web Series
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Search