import { IoMdNotificationsOutline } from 'react-icons/io';

const PrivacyPolicy = () => {
    return (
        <section className="privacy__policy">
            <h4 className="mb-2">Privacy policy</h4>
            <p>
                Add your privacy policy below. This explains to your customers
                how you will use the data that you collect from them. There will
                be a link to this from the footer of your box office.
            </p>
            <div className="notification">
                <div className="notification__icon">
                    <IoMdNotificationsOutline />
                </div>
                <div className="notification__content">
                    <h6>Do you need a privacy policy?</h6>
                    <p>
                        We have a created a free tool for you to create a custom
                        privacy policy specific to your requirements.
                    </p>
                    <a href="link__here">
                        Use our free privacy policy generator.
                    </a>
                </div>
            </div>
        </section>
    );
};

export default PrivacyPolicy;
