import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getHowtohostyourshows } from "../../../actions/How_to_host_your_show_actions";
import Metatag from "../../Metatag/Metatag";
import Title from "../../Shared/Title/Title";
import Video from "../../Video/Video";
import "./HostShow.css";


export default function HostShow(props) {
  const { info } = props;
  const dispatch = useDispatch();
  const getHowToShow = useSelector((state) => state.how_to_host);
  const { loading, error, how_to_host } = getHowToShow;
  useEffect(() => {
    dispatch(getHowtohostyourshows());
  }, []);
  return (
    <div className="add-margin">
      <Metatag metatag_name={"host_your_show"} info={info} />
      <Title title="HOW TO HOST SHOWS" />
      <div className="container">
        {how_to_host ? (
          <Video video={how_to_host} />
        ) : (
          <div></div>
        )}
        <div className="host__content">
          {how_to_host ? (
            <div>
              {how_to_host.map((how_to_host) => (
                <div
                  key={how_to_host.how_id}
                  dangerouslySetInnerHTML={{ __html: how_to_host.how_text }}
                />
              ))}
            </div>
          ) : (
            <div></div>
          )}
        </div>
      </div>
    </div>
  );
};
