import CKEditor from 'ckeditor4-react';
const EventForm = () => {
    return (
        <section className="basic__setting">
            <h4 className="mb-2">Event Form</h4>
            <div className="basic__setting__box">
                <form className="row g-3">
                    <div className="col-lg-6">
                        <label for="name" className="form-label">
                            Event Name *
                        </label>
                        <input
                            type="text"
                            className="form-control"
                            id="event_title"
                            name="event_title"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="startDate" className="form-label">
                            Start Date
                        </label>
                        <input
                            type="date"
                            className="form-control"
                            id="startDate"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="startDate" className="form-label">
                            End Date
                        </label>
                        <input
                            type="date"
                            className="form-control"
                            id="endDate"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="country" className="form-label">
                            Available Country
                        </label>
                        <textarea
                            type="text"
                            className="form-control"
                            id="country"
                        ></textarea>
                        <span className="msg">Seperate by coma</span>
                    </div>
                    <div className="col-lg-6">
                        <label for="country" className="form-label">
                            Country wise Date
                        </label>
                        <CKEditor
                            className="form-control"
                            id="countryDate"
                            name="countryDate"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="country" className="form-label">
                            Country wise Time
                        </label>
                        <CKEditor
                            className="form-control"
                            id="countryTime"
                            name="countryTime"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="country" className="form-label">
                            Currency Format
                        </label>
                        <select id="currency_format" className="form-control">
                            <option selected value="BDT">
                                BDT
                            </option>
                        </select>
                    </div>
                    <div className="col-lg-6">
                        <label for="price" className="form-label">
                            Price
                        </label>
                        <input
                            type="price"
                            className="form-control"
                            id="price"
                            name="price"
                        />
                    </div>
                    <div className="col-lg-12">
                        <label for="eventImage" className="form-label">
                            Event Image
                        </label>
                        <input
                            type="file"
                            className="form-control"
                            id="eventImage"
                            placeholder="1234 Main St"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="eventImage" className="form-label">
                            Event Trailer Video
                        </label>
                        <input
                            type="text"
                            className="form-control"
                            id="eventCoverImage"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="country" className="form-label">
                            Video Link
                        </label>
                        <input
                            type="text"
                            className="form-control"
                            id="videoLink"
                        />
                    </div>

                    <div className="col-lg-6">
                        <label for="description" className="form-label">
                            Description
                        </label>
                        <CKEditor
                            type="text"
                            className="form-control"
                            id="description"
                        />
                    </div>
                    <div className="col-lg-6">
                        <label for="inputAddress2" className="form-label">
                            Status
                        </label>
                        <select id="timeZone" className="form-control">
                            <option selected value="1">
                                Publish
                            </option>
                            <option value="0"> Draft </option>
                        </select>
                    </div>

                    <div className="col-12 submit__align">
                        <button type="submit" className="submit__btn">
                            Save
                        </button>
                    </div>
                </form>
            </div>
        </section>
    );
};

export default EventForm;
