import ReactImageAppear from 'react-image-appear';
import { Link } from 'react-router-dom';
import instance from '../../connection.js'

const NewsItem = (props) => {
    const { limited_blogs } = props
    return (
        <Link to={`/blog-details/${limited_blogs.blog_slug}`} className="news__item">
            <div className="news__item__img">
                <ReactImageAppear src={instance+`/${limited_blogs.blog_image}`} alt={limited_blogs.blog_alt} />
            </div>
            <div className="news__item__content">
                <h5 className="news__item__title">
                    {limited_blogs.title}
                </h5>
                <p>
                    <div dangerouslySetInnerHTML={{ __html: limited_blogs.description.substring(1, 80) + '...' }} />
                </p>
                <span className="news__item__date">{limited_blogs.date}</span>
            </div>
        </Link>
    );
};

export default NewsItem;
