import './Input.scss';

const Input = ({ type, label, labelTitle, required, placeholder }) => {
    return (
        <div className="custom__form__group">
            <label htmlFor={label} className="custom__form__label">
                {labelTitle} {/* {required && <span className="text-danger require ">*</span>} */}
            </label>
            <input id={label} type={type} required={required} placeholder={placeholder} className="custom__form__input" />
        </div>
    )
}

export default Input