import ReactOwlCarousel from 'react-owl-carousel';
import './Event.scss';
import EventItem from './EventItem';
import { option } from './Option';

const Events = ({ upcoming }) => {

    return (
        <div className="container  custom__slider__container event-wrapper  navs-right">
            <ReactOwlCarousel className="owl-theme pt-2" {...option}>
                {upcoming.map((upcoming) => (
                    <EventItem key={upcoming.event_id} upcoming={upcoming} />
                ))}
            </ReactOwlCarousel>
        </div>
    );
};

export default Events;
