import React, { useEffect } from "react";
import ReactImageAppear from "react-image-appear";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router";
import { getBlogDetails } from "../../actions/ticket_blogs_actions";
import Metatag from "../Metatag/Metatag";
import Title from "../Shared/Title/Title";
import "./Blog.scss";
import instance from '../../connection.js'


const BlogDetails = (props) => {
    const { info } = props;
    const { slug } = useParams();
    const dispatch = useDispatch();
    const blogDetails = useSelector((state) => state.getblogDetails);
    const { loading, error, blog_details } = blogDetails;
    
    useEffect(() => {
        dispatch(getBlogDetails(slug));
    }, []);
    return (
        <div className="add-margin">
            <Metatag metatag_name={slug} info={info} />
            <div className="blog-title-wrapper">
                <Title title="Blog" titleNumber={4} />
            </div>
            {blog_details ? (
                <div className="container">
                    {blog_details.map((blog_details) => (
                        <div key={blog_details.blog_id} className="blog__details__img">
                            <ReactImageAppear
                                src={instance+`/${blog_details.blog_image}`}
                                alt={blog_details.blog_alt}
                            />
                        </div>
                    ))}
                    <div className="blog__details__title">
                        {blog_details.map((blog_details) => (
                            <div key={blog_details.blog_id}>
                                <h3>{blog_details.title}</h3>
                                <span className="date">{blog_details.date}</span>
                            </div>
                        ))}
                    </div>
                    {blog_details.map((blog_details) => (
                        <div key={blog_details.blog_id} className="blog__details__option">
                            <div
                                className="text-justify"
                                key={blog_details.blog_id}
                                dangerouslySetInnerHTML={{ __html: blog_details.description }}
                            />
                        </div>
                    ))}
                </div>
            ) : (
                <div></div>
            )}
        </div>
    );
};

export default BlogDetails;
