import "./Video.scss";

export default function Video(props) {
  const { event_details } = props;
  return (
    <div className="video-container">

      <div className="video-player_one">
        <iframe className="video" src={"https://player.vimeo.com/video/" + event_details.main_video + "?badge=0&autoplay=1#t=1m2s&loop=1&autopause=0&player_id=0&app_id=58479"} frameBorder="0" allow="autoplay; fullscreen; picture-in-picture" allowFullScreen title="women.mp4"></iframe>
        <img
          src="/live.gif"
          alt=""
          className="close__btn"
        />
      </div>
    </div>
  );
}
