import CKEditor from 'ckeditor4-react';
import './settings.css';

const Home = () => {
    return (
        <section className="basic__setting">
            <h4 className="mb-2">Basic Setting</h4>
            <div className="basic__setting__box">
                <form className="row g-3">
                    <div className="col-md-6">
                        <label for="text" className="form-label">
                            Slider Text *
                        </label>
                        <CKEditor
                            className="form-control"
                            id="text"
                            name="text"
                        />
                    </div>
                    <div className="col-6">
                        <label for="image" className="form-label">
                            Slider Image
                        </label>
                        <input
                            type="file"
                            className="form-control"
                            id="image"
                            placeholder=""
                        />
                    </div>

                    <div className="col-md-6">
                        <label for="inputAddress2" className="form-label">
                            Status
                        </label>
                        <select id="timeZone" className="form-control">
                            <option selected value="1">
                                Publish
                            </option>
                            <option value="0"> Draft </option>
                        </select>
                    </div>

                    <div className="col-12 submit__align">
                        <button type="submit" className="submit__btn">
                            Save
                        </button>
                    </div>
                </form>
            </div>
        </section>
    );
};

export default Home;
