import { useState } from 'react';
import { FaTimes } from 'react-icons/fa';
import ReactModal from 'react-modal';
import OwlCarousel from 'react-owl-carousel';
import VipItem from './VipItem';
import './VipModal.scss';

ReactModal.setAppElement('#root');

const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: '80%',
        height: '95%',
    },
};

const VipModal = ({ modalIsOpen, setIsOpen }) => {
    const option = {
        loop: false,
        margin: 10,
        nav: true,
        responsiveClass: true,
        autoplay: false,
        dots: false,
        navText: [
            '<svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 320 512" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M34.52 239.03L228.87 44.69c9.37-9.37 24.57-9.37 33.94 0l22.67 22.67c9.36 9.36 9.37 24.52.04 33.9L131.49 256l154.02 154.75c9.34 9.38 9.32 24.54-.04 33.9l-22.67 22.67c-9.37 9.37-24.57 9.37-33.94 0L34.52 272.97c-9.37-9.37-9.37-24.57 0-33.94z"></path></svg>',
            '<svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 320 512" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M285.476 272.971L91.132 467.314c-9.373 9.373-24.569 9.373-33.941 0l-22.667-22.667c-9.357-9.357-9.375-24.522-.04-33.901L188.505 256 34.484 101.255c-9.335-9.379-9.317-24.544.04-33.901l22.667-22.667c9.373-9.373 24.569-9.373 33.941 0L285.475 239.03c9.373 9.372 9.373 24.568.001 33.941z"></path></svg>',
        ],
        responsive: {
            0: {
                items: 1,
            },
            600: {
                items: 2,
            },
            1000: {
                items: 3,
                nav: true,
            },
        },
    };

    const [vipItemSelect] = useState('one');


    // const vipItemHandler = (event) => {
    //     setVipItemSelect(event.target.value);
    // };

    function closeModal() {
        setIsOpen(false);
    }

    return (
        <ReactModal
            isOpen={modalIsOpen}
            onRequestClose={closeModal}
            style={customStyles}
            contentLabel="Example Modal"
        >
            <button className="quick-times-btn" onClick={closeModal}>
                <FaTimes />
            </button>
            <div className="mb-4">
                <h6>STEP 1 OF 3</h6>
                <h6>Choose the plan that’s right for you</h6>
                <h6>
                    Unlimited viewing on all your devices with everything
                    you like of ChillHall
                </h6>
            </div>


            <OwlCarousel className="owl-theme" {...option}>
                <div htmlFor="one">
                    <div
                        className={`${vipItemSelect === 'one' &&
                            'selectedItem'
                            } vipItem`}
                    >
                        <VipItem />
                    </div>
                </div>
                <div htmlFor="two">
                    <div
                        className={`${vipItemSelect === 'two' &&
                            'selectedItem'
                            } vipItem`}
                    >
                        <VipItem />
                    </div>
                </div>
                <div htmlFor="three">
                    <div
                        className={`${vipItemSelect === 'three' &&
                            'selectedItem'
                            } vipItem`}
                    >
                        <VipItem />
                    </div>
                </div>
            </OwlCarousel>
            <div className="text-center">
                <button className="custom__btn mt-4">CONTINUE</button>
            </div>
        </ReactModal>
    );
};

export default VipModal;
