export default function EventContent(props) {
  const { info } = props;
  return (
    <div className="about__content">
      <div className="about__option">
        {info ? (
          <div>
            {info.map((info) => (
              <div key={info.info_id} dangerouslySetInnerHTML={{ __html: info.create_event_text }} />
            ))}
          </div>
        ) : (
          <div></div>
        )}
      </div>
    </div>
  );
};

