import { useEffect, useState } from 'react';
import { HiSun } from 'react-icons/hi';

const DarkMode = () => {
    const [theme, setTheme] = useState(null);

    const { body } = document;
    const lightTheme = 'light';
    const darkTheme = 'dark';

    useEffect(() => {
        const getTheme = localStorage.getItem('theme-version');

        if (getTheme) {
            body.classList.add(getTheme);
            localStorage.setItem('theme-version', getTheme);
            setTheme(getTheme);
        } else {
            body.classList.add(darkTheme);
            localStorage.setItem('theme-version', darkTheme);
            setTheme(darkTheme);
        }
    }, [body.classList]);

    const switchTheme = () => {
        if (theme === darkTheme) {
            body.classList.replace(darkTheme, lightTheme);
            localStorage.setItem('theme-version', 'light');
            setTheme('light');
        } else {
            body.classList.replace(lightTheme, darkTheme);
            localStorage.setItem('theme-version', 'dark');
            setTheme('dark');
        }
    };

    return (
        <>
            <HiSun
                type="button"
                className={`toggle__btn ${theme === 'dark' ? 'light__btn' : 'dark__btn'
                    }`}
                onClick={(e) => switchTheme(e)}
            />
        </>
    );
};

export default DarkMode;
