import { Route, Switch, withRouter } from 'react-router';
import Sidebar from '../Shared/SettingSidebar/SettingSidebar';
import BasicSetting from './BasicSetting';
import Billing from './Billing';
import CheckoutForm from './CheckoutForm';
import EventForm from './EventForm';
import EventRequest from './EventRequest';
import Info from './info';
import OrderConfirm from './OrderConfirm';
import Payment from './Payment';
import PrivacyPolicy from './PrivacyPolicy';

const Settings = ({ match }) => {
    return (
        <div className="container mt-3 add-margin">
            <div className="row g-3">
                <div className="col-md-4 col-lg-3">
                    <Sidebar />
                </div>
                <div className="col-md-8 col-lg-9">
                    <div className="profile__routes__content p-4 shadow-sm mb-5">
                        <Switch>
                            <Route exact path={`${match.path}/`}>
                                <BasicSetting />
                            </Route>

                            <Route path={`${match.path}/event-form`}>
                                <EventForm />
                            </Route>
                            <Route path={`${match.path}/event-request`}>
                                <EventRequest />
                            </Route>
                            <Route path={`${match.path}/info`}>
                                <Info />
                            </Route>
                            <Route path={`${match.path}/checkout-form`}>
                                <CheckoutForm />
                            </Route>
                            <Route path={`${match.path}/order-confirm`}>
                                <OrderConfirm />
                            </Route>
                            <Route path={`${match.path}/privacy-policy`}>
                                <PrivacyPolicy />
                            </Route>
                            <Route path={`${match.path}/billing`}>
                                <Billing />
                            </Route>
                            <Route path={`${match.path}/payment`}>
                                <Payment />
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default withRouter(Settings);
