import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
    getALLEvents,
    getUpcomingEvents
} from "../../../actions/upcoming_events_actions";
import EventItem from "../../Event/EventItem";
import Title from "../../Shared/Title/Title";
import "./TopEvents.scss";

const UpcomingEvents = (props) => {
    const dispatch = useDispatch();

    const allEvents_data = useSelector((state) => state.getAllEvents);
    const { allevents } = allEvents_data;
    useEffect(() => {
        dispatch(getALLEvents());
    }, [dispatch]);

    const upcomingEvents = useSelector((state) => state.getUpcomingEvents);
    const { upcoming } = upcomingEvents;
    useEffect(() => {
        dispatch(getUpcomingEvents());
    }, []);
    const { events } = props;
    return (
        <div className="top__events add-margin">
            <Title title="Upcoming Events" />
            {allevents ? (
                <div className="container">
                    <div className="row g-2 g-md-3">
                        {allevents.map((upcoming) => (
                            <div className="col-6 col-sm-6 col-md-4 col-lg-3 remove-zoom" key={upcoming.event_id}>
                                <EventItem upcoming={upcoming} />
                            </div>
                        ))}
                    </div>
                </div>
            ) : (
                <div></div>
            )}
        </div>
    );
};

export default UpcomingEvents;
