import {UPCOMING_EVENTS_LIST_REQUEST,UPCOMING_EVENTS_LIST_SUCCESS,UPCOMING_EVENTS_LIST_FAIL,ALL_EVENTS_LIST_REQUEST,ALL_EVENTS_LIST_SUCCESS,ALL_EVENTS_LIST_FAIL} from '../constants/upcoming_events_constant';

export const upcomingEventsReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case UPCOMING_EVENTS_LIST_REQUEST:
            return {loading:true};

            case UPCOMING_EVENTS_LIST_SUCCESS:
                return {loading:false, upcoming: action.payload};

                case UPCOMING_EVENTS_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}

export const allEventsReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case ALL_EVENTS_LIST_REQUEST:
            return {loading:true};

            case ALL_EVENTS_LIST_SUCCESS:
                return {loading:false, allevents: action.payload};

                case ALL_EVENTS_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}