import React, { useState } from "react";
import { AiFillEye, AiFillEyeInvisible } from "react-icons/ai";
import { FaEnvelope, FaLock, FaUser } from "react-icons/fa";
import { Link } from "react-router-dom";
import appleImage from "../../assets/images/apple.png";
import facebookImage from "../../assets/images/facebook.png";
import googleImage from "../../assets/images/search.png";
import "./Auth.scss";
import { useAlert } from 'react-alert'
import Axios from "axios";
import auth from "../../auth_handler";
import { useHistory } from "react-router-dom";
import instance from '../../connection.js'



const Auth = () => {
    const history = useHistory();
    const alert = useAlert()
    const [newUser, setNewUser] = useState(false);
    const [show_password, setShow_password] = useState(false);

    const [name, setName] = useState('');
    const [email, setemail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('')


    const [loginemail, setLoginemail] = useState('');
    const [loginpassword, setLoginPassword] = useState('');
    if(auth.isAuthenticated()){
        history.push('/');
    }

    const register_function = (props) => {
        if (newUser) {
            if (name !== "" && email !== "" && password !== "" && confirmPassword !== "") {
                if (password.length < 6) {

                    alert.show('Password should be at least 6 character long')
                } else {
                    if (password !== confirmPassword) {
                        alert.error("Both Passwords are not same");
                    } else {
                        alert.show("Processing...");
                        Axios.post(instance+'/auth/register', { name: name, email: email, password: password }).then((response) => {
                            if (String(response.data.message) === '1') {
                                alert.show('Registration Successfull');
                                setNewUser(response);
                            } else {
                                alert.show('Email already in use');
                            }
                        })
                    }
                }
            } else {
                alert.show("Please fill up all fields.");
            }
        } else {
            if (email !== "" && password !== "") {
                alert.show("Processing...");
                Axios.post(instance+'/auth/login', { email: email, password: password }).then((response) => {
                    console.log(response);
                    if (String(response.data.message) === '1') {
                        alert.show('Login Successfull');
                        localStorage.setItem('userid', response.data.result.user_id);
                        localStorage.setItem('username', response.data.result.name);
                        localStorage.setItem('email', response.data.result.email);
                        setNewUser(response);
                        auth.login(() => {
                            history.push("/");
                          });
                    } else {
                        alert.show('Invalid Email/Password');
                    }
                })
            }else{
                alert.show('Please fill up all fields.');
            }
        }
    }

    return (
        <section className="form-section add-margin mt-3" style={{
            backgroundImage: `url("/BG_Doodle_png.png")`
        }}>
            <div className="container custom_pading">
                <div className="row">
                    <div className=" col-sm-10 col-md-7 col-lg-5 mx-auto">
                        <div className="form-container contact__form__wrapper p-0">
                            <div className="form__tabs">
                                <span
                                    className={`form__tabs__option ${!newUser && "addBorder"}`}
                                    onClick={() => setNewUser(!newUser)}
                                >
                                    Login
                                </span>
                                <span className="or"> OR </span>
                                <span
                                    className={`form__tabs__option ${newUser && "addBorder"}`}
                                    onClick={() => setNewUser(!newUser)}
                                >
                                    Sign Up
                                </span>
                            </div>
                            <div className="form-input-wrapper">
                                {newUser && (
                                    <>
                                        <div className="input-item">
                                            <label className="input-label">Name</label>
                                            <div className="input-field">
                                                <span className="input-icon">
                                                    <FaUser />
                                                </span>
                                                <input
                                                    type="text"
                                                    name="name"
                                                    id="name"
                                                    placeholder="eg. chillhall"
                                                    onChange={(e) => {
                                                        setName(e.target.value)
                                                    }}
                                                />
                                            </div>
                                        </div>
                                    </>
                                )}
                                <div className="input-item">
                                    <label className="input-label">
                                        Email ID Or Mobile Number
                                    </label>
                                    <div className="input-field">
                                        <span className="input-icon">
                                            <FaEnvelope />
                                        </span>
                                        <input
                                            type="email"
                                            name="email"
                                            id="email"
                                            placeholder="eg. jhon@example.com"
                                            onChange={(e) => {
                                                setemail(e.target.value)
                                            }}
                                        />
                                    </div>
                                </div>
                                <div className="input-item">
                                    <label className="input-label">Password</label>
                                    <div className="input-field">
                                        <span className="input-icon">
                                            <FaLock />
                                        </span>
                                        <input
                                            type={show_password ? "text" : "password"}
                                            name="password"
                                            id="password"
                                            placeholder="Password"
                                            onChange={(e) => {
                                                setPassword(e.target.value);
                                            }}
                                        />
                                        <div onClick={(e) => setShow_password(!show_password)}>
                                            {show_password ? (
                                                <AiFillEye className="show__password" />
                                            ) : (
                                                <AiFillEyeInvisible className="show__password" />
                                            )}
                                        </div>
                                    </div>
                                </div>
                                {newUser && (
                                    <small
                                        className="d-block pb-3 "
                                        style={{ marginTop: "-12px" }}
                                    >
                                        Password must be lowercase, uppercase and numbers
                                    </small>
                                )}
                                {newUser && (
                                    <div className="input-item">
                                        <label className="input-label">Confirm Password</label>
                                        <div className="input-field">
                                            <span className="input-icon">
                                                <FaLock />
                                            </span>
                                            <input
                                                type="password"
                                                name="confirmPassword"
                                                id="confirmPassword"
                                                placeholder="Confirm Password"
                                                onChange={(e) => {
                                                    setConfirmPassword(e.target.value);
                                                }}
                                            />
                                        </div>
                                    </div>
                                )}

                                {newUser && (
                                    <Link to="/forget" className="forget__password">
                                        Forget your Password?
                                    </Link>
                                )}

                                {
                                    <div className="btn-style">
                                        <button
                                            onClick={register_function}
                                            className="custom__submit__btn w-100"
                                            type="submit"
                                        >
                                            {newUser ? "Register" : "Login"}
                                        </button>
                                    </div>
                                }

                                <div className="auth__with">
                                    <div className="auth-with-item">
                                        <img src={facebookImage} alt="" />
                                    </div>
                                    <div className="auth-with-item">
                                        <img src={googleImage} alt="" />
                                    </div>
                                    <div className="auth-with-item">
                                        <img width="32px" src={appleImage} alt="" />
                                    </div>
                                </div>

                                <div className={`have-account ${!newUser && "pt-3 mt-1"}`}>
                                    <span>
                                        {newUser
                                            ? "Already Have an Account?"
                                            : "Have not account yet?"}
                                    </span>
                                    <span
                                        onClick={() => setNewUser(!newUser)}
                                        className="sign-up"
                                    >
                                        {newUser ? "Login" : "Sign Up"}
                                    </span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Auth;
