const AddBuyerQns = () => {
    return (
        <div
            className=" fade"
            id="addBuyerQns"
            data-bs-backdrop="static"
            data-bs-keyboard="false"
            tabindex="-1"
            aria-labelledby="addBuyerQns"
            aria-hidden="true"
        >
            <div className="-dialog">
                <div className="-content">
                    <div className="-header">
                        <h5 id="addBuyerQns">Buyer only question</h5>
                        <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div className="-body">
                        <div className="mb-3">
                            <label for="timeZone" className="form-label">
                                Date Formate
                            </label>
                            <select id="timeZone" className="form-control">
                                <option selected> Thu 15 Apr 2021 </option>
                                <option>Thu Apr 15, 2021</option>
                            </select>
                        </div>
                        <div className="mb-3">
                            <label for="inputAddress" className="form-label">
                                What question do you want to ask? *
                            </label>
                            <input
                                type="text"
                                className="form-control"
                                id="inputAddress"
                                placeholder="1234 Main St"
                            />
                        </div>

                        <div className="mb-3">
                            <label for="answer" className="form-label">
                                Options for answer (Enter one answer per line)
                            </label>
                            <textarea
                                className="form-control"
                                id="answer"
                                rows="3"
                            ></textarea>
                        </div>
                    </div>
                    <div className="-footer">
                        <button
                            type="button"
                            id="cancel"
                            data-bs-dismiss="modal"
                        >
                            Close
                        </button>
                        <button type="button" id="submit">
                            Save Custom Question
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AddBuyerQns;
