import {BLOG_LIST_REQUEST,BLOG_LIST_SUCCESS,BLOG_LIST_FAIL, BLOG_LIST_LIMITED_REQUEST, BLOG_LIST_LIMITED_SUCCESS, BLOG_LIST_LIMITED_FAIL,BLOG_DETAILS_REQUEST,BLOG_DETAILS_SUCCESS,BLOG_DETAILS_FAIL} from '../constants/ticket_blogs_constants';
import Axios from 'axios';
import instance from '../connection.js'

export const getAllBlogs = () => async(dispactch)=>{
    dispactch({
        type:BLOG_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/blogs');
        dispactch({type: BLOG_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:BLOG_LIST_FAIL,payload: error.message});
    }
}

export const getLimitedBlogs = () => async(dispactch)=>{
    dispactch({
        type:BLOG_LIST_LIMITED_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/blogs-limited');
        dispactch({type: BLOG_LIST_LIMITED_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:BLOG_LIST_LIMITED_FAIL,payload: error.message});
    }
}

export const getBlogDetails = (slug) => async(dispactch)=>{
    dispactch({
        type:BLOG_DETAILS_REQUEST
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/blog-details',{slug:slug});
        dispactch({type: BLOG_DETAILS_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:BLOG_DETAILS_FAIL,payload: error.message});
    }
}

