import { DiscussionEmbed } from "disqus-react";
import React, { useEffect } from "react";
import ReactStars from "react-rating-stars-component";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { eventDetailsActions } from "../../../actions/event_details_actions";
import Metatag from "../../Metatag/Metatag";
import Title from "../../Shared/Title/Title";
import Video from "../../Video/event";
import "./EventDetails.scss";

const EventMain = (props) => {
	const { slug } = useParams();
	const { info } = props;

	const dispatch = useDispatch();
	const details = useSelector((state) => state.getEventDetails);
	const { loading, error, event_details } = details;
	useEffect(() => {
		dispatch(eventDetailsActions(slug));
	}, []);

	const ratingChanged = (newRating) => {
		console.log(newRating);
	};

	return (
		<>
			<Metatag metatag_name={slug} info={info} />
			<div>
				{event_details ? (
					<div>
						{event_details.map((event_details) => (
							<div key={event_details.event_id}>
								<Title
									title={event_details.event_title}
									titleNumber={3}
									link={event_details.event_slug}
								/>
								<div>
									<div className="container pt-3">
										<Video event_details={event_details} />
									</div>
								</div>
								<div className="related__event container pt-5" >
									<center><table>
										<tr>
											<td><h4>Your valueable Review : &nbsp;</h4></td>
											<td><ReactStars
												count={5}
												onChange={ratingChanged}
												size={40}
												isHalf={true}
												emptyIcon={<i className="far fa-star"></i>}
												halfIcon={<i className="fa fa-star-half-alt"></i>}
												fullIcon={<i className="fa fa-star"></i>}
												activeColor="#ED1B24"
											/></td>
										</tr>
									</table></center>
								</div>

								{/* ******* Related Events ******** */}
							</div>
						))}
					</div>
				) : (
					<div></div>
				)}
				{info ? (
					<div className="related__event">
						<Title title="Any Query" />
						{info.map((info) => (
							<h4 key={info.info_id} className="container">
								Whatsapp: {info.whatsapp}
							</h4>
						))}
					</div>
				) : (
					<div></div>
				)}
				<br />
				<br />
				<div className="container comment-mt-10">
					<DiscussionEmbed
						shortname="chillhall-com"
						config={{
							url: "http://localhost:3000/EventMain/" + slug,
							identifier: slug,
							title: slug,
							language: "zh_EN", //e.g. for Traditional Chinese (Taiwan)
						}}
					/>
				</div>
			</div>
		</>
	);
};

export default EventMain;
