import { SLIDER_LIST_REQUEST,SLIDER_LIST_SUCCESS,SLIDER_LIST_FAIL} from "../constants/ticket_home_slider_constants";
import Axios from 'axios';
import instance from '../connection.js'

export const listSliders = () => async(dispactch)=>{
    dispactch({
        type:SLIDER_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/sliders');
        dispactch({type: SLIDER_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:SLIDER_LIST_FAIL,payload: error.message});
    }
}
