import {
    FaEnvelope,
    FaFacebookF,
    FaInstagram,
    FaPhoneVolume,
    FaTwitter,
    FaWhatsapp,
    FaYoutube
} from "react-icons/fa";
import ReactImageAppear from "react-image-appear";
import { Link } from "react-router-dom";
import Metatag from "../../Metatag/Metatag";
import Title from "../../Shared/Title/Title";
import "./Contact.css";
import instance from '../../../connection.js'

export default function Contact(props) {
    const { info } = props;
    return (
        <div className="add-margin">
            <Metatag metatag_name={"contact"} info={info} />

            <Title title="CONTACT" />

            <div className="container">
                <div className="row g-4">
                    <div className="col-lg-6 order-1 order-lg-0">
                        {info ? (
                            <div className="contact__img d-none d-lg-block ">
                                {info.map((info) => (
                                    <ReactImageAppear key={info.info_id}
                                        src={instance+`/${info.contact_page_image}`}
                                        alt={info.about_alt}
                                    />
                                ))}
                            </div>
                        ) : (
                            <div></div>
                        )}
                    </div>
                    <div className="col-lg-6 order-0 order-lg-1">
                        <div className="contact__form__wrapper">
                            {info ? (
                                <div className="contact__info">
                                    {info.map((info) => (
                                        <div key={info.info_id}>
                                            <h6>Information</h6>
                                            <div className="contact__info__option">
                                                <FaPhoneVolume />
                                                <span><a href={`tel:${info.phone}`} className="follow__icon fs-6">{info.phone}</a></span>
                                            </div>
                                            <div className="contact__info__option">
                                                <FaEnvelope />
                                                <span><a href={`mailto:${info.email}`} className="follow__icon fs-6">{info.email}</a></span>
                                            </div>
                                            <div className="contact__info__option">
                                                <FaWhatsapp />
                                                <span> <Link to={{ pathname: `https://api.whatsapp.com/send?phone=${info.whatsapp}` }} className="follow__icon fs-6" target="_blank">{info.whatsapp}</Link></span>
                                            </div>
                                            <div className="follow__icons">
                                                <Link to={{ pathname: info.facebook }} className="follow__icon" target="_blank">
                                                    <FaFacebookF />
                                                </Link>
                                                <Link to={{ pathname: info.twitter }} className="follow__icon" target="_blank">
                                                    <FaTwitter />
                                                </Link>
                                                <Link to={{ pathname: info.instagram }} className="follow__icon" target="_blank">
                                                    <FaInstagram />
                                                </Link>
                                                <Link to={{ pathname: info.youtube }} className="follow__icon" target="_blank">
                                                    <FaYoutube />
                                                </Link>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            ) : (
                                <div></div>
                            )}
                            <form className="contact__form">
                                <div className="row">
                                    <div className="col-md-6  ">
                                        <div className="custom__form__group">
                                            <label htmlFor="" className="custom__form__label">
                                                Your Name
                                            </label>
                                            <input type="text" className="custom__form__input" />
                                        </div>
                                        <div className="custom__form__group">
                                            <label htmlFor="" className="custom__form__label">
                                                Email
                                            </label>
                                            <input type="text" className="custom__form__input" />
                                        </div>
                                        <div className="custom__form__group">
                                            <label htmlFor="" className="custom__form__label">
                                                Contact Number
                                            </label>
                                            <input type="text" className="custom__form__input" />
                                        </div>
                                    </div>
                                    <div className="col-md-6">
                                        <div className="custom__form__group">
                                            <label htmlFor="" className="custom__form__label">
                                                Message
                                            </label>
                                            <textarea
                                                name=""
                                                id=""
                                                rows="8"
                                                className="custom__form__input"
                                            ></textarea>
                                        </div>
                                    </div>
                                </div>
                                <div className="contact__submit text-center pt-2">
                                    <button type="submit" className="custom__submit__btn">
                                        Submit
                                    </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
