import { META_LIST_REQUEST,META_LIST_SUCCESS,META_LIST_FAIL} from "../constants/meta_tag_constants";
import Axios from 'axios';

import instance from '../connection.js'

export const getMeta = (page_name) => async(dispactch)=>{
    dispactch({
        type:META_LIST_REQUEST
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/metatag',{name:page_name});
        dispactch({type: META_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:META_LIST_FAIL,payload: error.message});
    }
}
