import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { eventDetailsActions } from "../../../actions/event_details_actions";
import Metatag from "../../Metatag/Metatag";
import EventTitle from "../../Shared/EventTitle/EventTitle";
import Title from "../../Shared/Title/Title";
import Video from "../../Video/trailer";
import EventDate from "./EventDate";
import EventDescription from "./EventDescription";
import "./EventDetails.scss";
import EventPrice from "./EventPrice";

const EventDetails = (props) => {
    const { slug } = useParams();
    const { info } = props;


    const dispatch = useDispatch();
    const details = useSelector((state) => state.getEventDetails);
    const { loading, error, event_details } = details;
    useEffect(() => {
        dispatch(eventDetailsActions(slug));
    }, []);
    return (
        <div className="add-margin">
            <Metatag metatag_name={slug} info={info} />
            <div>
                {event_details ? (
                    <>
                        {event_details.map((event_details) => (
                            <div key={event_details.event_id}>
                                <div className="wrapping-event-title">
                                    <Title title={event_details.event_title} titleNumber={2} link={event_details.event_slug} />
                                </div>
                                <EventDescription description={event_details} />
                                <div className="container">
                                    <div className="row ">
                                        <div className="col-md-12 col-lg-6">
                                            <EventTitle title="PRICE OF EVENT" type={1} />
                                            <EventPrice price={event_details} />
                                        </div>
                                        <div className="col-md-12 col-lg-6">
                                            <EventTitle title="DATE OF EVENT" type={2} />
                                            <EventDate date={event_details} />
                                        </div>
                                    </div>
                                </div>

                                {/* ******* TRAILER VIDEO  ******** */}
                                <div>
                                    <Title title="TRAILER VIDEO" />
                                    <div className="container pt-3">
                                        <Video event_details={event_details} />
                                    </div>
                                </div>
                            </div>
                        ))}
                    </>
                ) : (
                    <div></div>
                )}
            </div>
        </div>
    );
};

export default EventDetails;
