import { Doughnut } from 'react-chartjs-2';

const DonateChart = ({ data }) => {
    return (
        <Doughnut
            data={{
                labels: ['bKash', 'Nagad', 'DBBL Mobile'],
                datasets: [
                    {
                        label: 'My First Dataset',
                        data: data,
                        backgroundColor: [
                            'rgb(255, 99, 132)',
                            'rgb(54, 162, 235)',
                            'rgb(255, 205, 86)',
                        ],
                        hoverOffset: 4,
                    },
                ],
            }}
            height={300}
            width={300}
        />
    );
};

export default DonateChart;
