import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createEventActions } from "../../actions/creat_event_actions";
import Input from "../Input/Input";
import Select from "../Input/Select";
import { useAlert } from 'react-alert';


const VirtualForm = ({ query }) => {

    const alert = useAlert();

    const [countryRow, setCountryRow] = useState([0]);
    const [priceRow, setPriceRow] = useState([0]);
    const platform = [
        { id: 1, value: 'Chill Hall', label: 'Chill Hall' },
    ]
    const genres = [
        { id: 1, value: 'Digital Concert', label: 'Digital Concert' },
        { id: 2, value: 'Donation/Charity', label: 'Donation/Charity ' },
        { id: 3, value: 'Theatre Show', label: 'Theatre Show' },
        { id: 4, value: 'Others', label: 'Others' },
    ]


    var description;
    var status;


    const dispatch = useDispatch();
    const createEventData = useSelector((state) => state.getCreateEvent);
    var { create_event } = createEventData;

    if (create_event) {
        if (create_event.affectedRows > 0) {
            if(status){
                document.getElementById("eventName").value = "";
                document.getElementById("startDate").value = "";
                document.getElementById("endDate").value = "";
                document.getElementById("genres").value = "";
                document.getElementById("event_link").value = "";
                document.getElementById("hear_about").value = "";
                document.getElementById("email").value = "";
                document.getElementById("contactNumber").value = "";
                document.getElementById("platform").value = "";
                document.getElementById("eventtime").value = "";
            }
            status = false;
            description = "";

            alert.show("Your request is received. We'll contact with you soon.");
        }
    }

    function PostData() {
        var event_name = document.getElementById("eventName").value;
        var start_date = document.getElementById("startDate").value;
        var end_date = document.getElementById("endDate").value;
        var genres = document.getElementById("genres").value;
        var event_link = document.getElementById("event_link").value;
        var hear_about = document.getElementById("hear_about").value;
        var email = document.getElementById('email').value;
        var contactNumber = document.getElementById('contactNumber').value;
        var platform = document.getElementById('platform').value;
        var eventtime = document.getElementById('eventtime').value;
        if (
            event_name !== "" &&
            genres !== "" &&
            event_link !== "" &&
            description !== "" &&
            email !== "" &&
            contactNumber !== ""
        ) {
            dispatch(
                createEventActions(
                    event_name,
                    start_date,
                    end_date,
                    genres,
                    event_link,
                    hear_about,
                    email,
                    contactNumber,
                    platform,
                    description,
                    eventtime
                )
            );
        } else {
            alert.show("Marked Fields are required");
        }
    };

    return (
        <div className="form-wrapper">
            <div className="event-form contact__form__wrapper">
                <div className="row">
                    <div className="col-12">
                        <Input
                            label="eventName"
                            labelTitle="Event Name *"
                            name="eventName"
                            id="eventName"
                            type="text"
                            required
                        />
                    </div>

                    <div className="col-12 col-md-6">
                        <Input
                            label="eventtime"
                            labelTitle="Event Time (optional)"
                            name="eventtime"
                            id="eventtime"
                            type="time"
                            required
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Select
                            label="platform"
                            labelTitle="Platform (optional)"
                            options={platform}
                            id="platform"
                            required
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="startDate"
                            labelTitle="Start Date (optional)"
                            name="startDate"
                            id="startDate"
                            type="date"
                            required
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="endDate"
                            labelTitle="End Date (optional)"
                            name="endDate"
                            id="endDate"
                            type="date"
                            required
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Select
                            label="genres"
                            labelTitle="Genres *"
                            options={genres}
                            id="Genres"
                            required
                        />
                    </div>

                    <div className="col-12 col-md-6">
                        <div className="custom__form__group">
                            <label htmlFor="" className="custom__form__label">
                                Share You Event Link *
                            </label>
                            <input
                                type="text"
                                name="event_link"
                                id="event_link"
                                placeholder="Share You Event Link"
                                className="custom__form__input"
                            />
                        </div>
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="email"
                            labelTitle="Email Address *"
                            name="email"
                            id="email"
                            type="email"
                            required
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="contactNumber"
                            labelTitle="Contact Number *"
                            name="contactNumber"
                            id="contactNumber"
                            type="type"
                            required
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <div className="custom__form__group">
                            <label htmlFor="" className="custom__form__label">
                                How did you hear about us? 
                            </label>
                            <input
                                type="text"
                                name="hear_about"
                                id="hear_about"
                                className="custom__form__input"
                                placeholder="How did you hear about us?"
                            />
                        </div>
                    </div>



                    <div className="col-12 ">
                        <div className="custom__form__group">
                            <label htmlFor="" className="custom__form__label">
                                Requirement Details (optional)
                            </label>
                            <CKEditor
                                className="height"
                                editor={ClassicEditor}
                                data={description}
                                name="description"
                                onChange={(event, editor) => {
                                    description = editor.getData();
                                }}
                            />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="custom__form__group text-center mt-2">
                            <button
                                type="submit"
                                className="custom__submit__btn"
                                onClick={PostData}
                            >
                                Submit
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default VirtualForm;
