import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { artistAction } from "../../../actions/artists_actions";
import Profile from "../../Profile/Profile";
import Title from "../../Shared/Title/Title";

const Artists = () => {
    const dispatch = useDispatch();
    const artists_data = useSelector((state) => state.getartist);
    const { artists } = artists_data;
    useEffect(() => {
        dispatch(artistAction());
    }, [dispatch]);
    return (
        <div className="add-margin">
            <Title
                title="HIRE AN ARTIST "
                link="/artists"
                titleNumber={3}
            />
            <div className="container">
                <div className="row g-3">
                    {artists && artists.map((artists) => (
                        <div className="col-6 col-sm-4 col-md-3" key={artists.artist_id}>
                            <Profile artists={artists} />
                        </div>
                    ))}
                </div>
            </div>
        </div>
    )
}

export default Artists