import { Link } from "react-router-dom";
import auth from "../../auth_handler";
import CartStore from "../../cartstore";
import { useEffect, useState } from 'react';
import { useAlert } from 'react-alert'

const TicketForm = ({ setStep }) => {

    const alert = useAlert()

    function stepchecker(){
        var qty = document.getElementById('qty').value;
        var name = document.getElementById('fullname').value;
        var caremail = document.getElementById('caremail').value;
        var caremail = document.getElementById('caremail').value;
        var ccode = document.getElementById('ccode').value;
        var cartmobile = document.getElementById('cartmobile').value;

        if(
            qty!=="" &&
            name !=="" &&
            caremail !== "" &&
            ccode !== "" &&
            cartmobile !==""

        ){
            setStep(2);
        }else{
            alert.show("Please fill up all fields.");
        }
        
    }

    return (
        <div className="ticketForm">
            <div className="custom__form__group">
                <label htmlFor="" className="custom__form__label">
                    Quantity
                </label>
                <input
                    type="text"
                    name=""
                    id="qty"

                    placeholder="(Example: 01)"
                    className="custom__form__input"
                />
            </div>
            <div className="custom__form__group">
                <label htmlFor="" className="custom__form__label">
                    Full Name
                </label>
                <input
                    type="text"
                    name=""
                    id="fullname"
                    onKeyUp={(e) => {CartStore.setfullname(e.target.value)}}
                    placeholder="(Example: Shahad Shareef)"
                    className="custom__form__input"
                />
            </div>

            <div className="row p-0">
                <div className="col-lg-6">
                    <div className="custom__form__group">
                        <label htmlFor="" className="custom__form__label">
                            Email
                        </label>
                        <input
                            type="email"
                            name=""
                            id="caremail"
                            value={auth.getemail()}
                            onKeyUp={(e) => {CartStore.setcartEmail(e)}}
                            placeholder="(Example: chillhall@gmail.com)"
                            className="custom__form__input"
                        />
                    </div>
                </div>
                <div className="col-lg-6">
                    <div className="custom__form__group">
                        <label htmlFor="" className="custom__form__label">
                            Age
                        </label>
                        <input
                            type="text"
                            name=""
                            onKeyUp={(e) => {CartStore.setAge(e.target.value)}}
                            placeholder="Enter your Age (eg. 20)"
                            className="custom__form__input"
                        />
                    </div>
                </div>
            </div>
            <div className="row p-0">
                <div className="col-lg-4">
                    <div className="custom__form__group">
                        <label htmlFor="" className="custom__form__label">
                            Code
                        </label>
                        <input
                            type="text"
                            name=""
                            id="ccode"
                            onKeyUp={(e) => {CartStore.setcode(e.target.value)}}
                            placeholder="(+8801)"
                            className="custom__form__input"
                        />
                    </div>
                </div>
                <div className="col-lg-8">
                    <div className="custom__form__group">
                        <label htmlFor="" className="custom__form__label">
                            Mobile Number
                        </label>
                        <input
                            type="text"
                            name=""
                            id="cartmobile"
                            onKeyUp={(e) => {CartStore.setmobile(e.target.value)}}
                            placeholder="(Example: +8801 xxx - xxx xxx)"
                            className="custom__form__input"
                        />
                    </div>
                </div>
            </div>
            <hr className="my-2" />
            <div className="custom__form__group d-flex ">
                <input type="checkbox" name="" className="mt-1" />
                <label htmlFor="" className="custom__form__label mb-0 ms-2">
                    I accept and have read the <Link to="/term-and-condition" style={{ color: 'var(--primary)', textDecoration: 'underline' }}>Term and Condition</Link>
                </label>
            </div>
            <div className="custom__form__group text-center">

                <button className="custom__submit__btn mt-3 " onClick={() => stepchecker() } >Continue</button>
            </div>
        </div>
    );
};

export default TicketForm;
