import React, { useEffect, useState } from 'react';
import ReactImageAppear from 'react-image-appear';
import { useDispatch, useSelector } from 'react-redux';
import { listInfo } from '../../actions/ticket_info_actions';
import instance from '../../connection';
import './settings.css';

const Home = () => {
    const [showMail, setShowMail] = useState(false);
    const dispatch = useDispatch();
    const getinfo = useSelector((state) => state.getinfo);
    const { loading, error, info } = getinfo;
    useEffect(() => {
        dispatch(listInfo());
    }, []);
    return (
        <>
            {info ? (
                <div>
                    {info.map((info) => (
                        <section key={info.info_id} className="basic__setting">
                            <h2>Event Request</h2>
                            <div className="">
                                <table class="table">
                                    <tbody>
                                        <tr>
                                            <th>Buisness Name</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    value={info.shop_name}
                                                    type="text"
                                                    id="shop_name"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Logo</th>
                                            <td>
                                                <ReactImageAppear
                                                    src={instance+`/${info.logo}`}
                                                    alt={info.logo_alt}
                                                    className="img_width"
                                                />
                                                <br></br>
                                                <input
                                                    type="file"
                                                    className="form-control"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Favicon</th>
                                            <td>
                                                <ReactImageAppear
                                                    src={instance+`/${info.fav}`}
                                                    alt={info.logo_alt}
                                                    className="img_width"
                                                />
                                                <br></br>
                                                <input
                                                    type="file"
                                                    className="form-control"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Footer Logo</th>
                                            <td>
                                                <ReactImageAppear
                                                    src={instance+`/${info.footer_logo}`}
                                                    alt={info.logo_alt}
                                                    className="img_width"
                                                />
                                                <br></br>
                                                <input
                                                    type="file"
                                                    className="form-control"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Email</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    value={info.email}
                                                    type="text"
                                                    id="email"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Whatsapp</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    value={info.whatsapp}
                                                    type="text"
                                                    id="whatsapp"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Number</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    value={info.phone}
                                                    type="text"
                                                    id="number"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Facebook</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    value={info.facebook}
                                                    type="text"
                                                    id="fb"
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Instagram</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    type="text"
                                                    id="insta"
                                                    value={info.instagram}
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Twitter</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    type="text"
                                                    id="twitter"
                                                    value={info.twitter}
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Youtube</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    type="text"
                                                    id="twitter"
                                                    value={info.youtube}
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Apple Store</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    type="text"
                                                    id="appstore"
                                                    value={info.appstore}
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Play Store</th>
                                            <td>
                                                <input
                                                    className="form-control"
                                                    type="text"
                                                    id="appstore"
                                                    value={info.playstore}
                                                />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>About Us Text</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={info.about_us}
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Footer Text</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={info.footer_text}
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Privacy Policy</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={info.privacy}
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Refund Policy</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={info.refund}
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Cancel Policy</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={info.cancel}
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Create Event Text</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={
                                                        info.create_event_text
                                                    }
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>Find My Token Text</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={
                                                        info.find_my_token_text
                                                    }
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>How to buy Tickets</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={
                                                        info.how_to_buy_tickets
                                                    }
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>HOW TO HOST SHOWS</th>
                                            <td>
                                                <textarea
                                                    className="form-control"
                                                    value={
                                                        info.how_to_host_shows
                                                    }
                                                ></textarea>
                                            </td>
                                        </tr>
                                        <tr>
                                            <th>About Us Image</th>
                                            <td>
                                                <ReactImageAppear
                                                    src={instance+`/${info.about_image}`}
                                                    alt={info.about_alt}
                                                    className="img_width"
                                                />

                                                <br></br>
                                                <input
                                                    type="file"
                                                    className="form-control"
                                                />
                                            </td>
                                        </tr>
                                        .
                                        <tr>
                                            <th>Contact Image</th>
                                            <td>
                                                <ReactImageAppear
                                                    src={instance+`/${info.contact_page_image}`}
                                                    alt={info.about_alt}
                                                    className="img_width"
                                                />
                                                <br></br>
                                                <input
                                                    type="file"
                                                    className="form-control"
                                                />
                                            </td>
                                        </tr>
                                    </tbody>
                                    <tr>
                                        <th></th>
                                        <td>
                                            <input
                                                type="submit"
                                                class="btn btn-primary color_dark"
                                                value="Save"
                                            ></input>
                                        </td>
                                    </tr>
                                </table>
                            </div>
                        </section>
                    ))}
                </div>
            ) : (
                <div></div>
            )}
        </>
    );
};

export default Home;
