import image from '../../../assets/images/submit-content.png';
import Title from '../../Shared/Title/Title';
import './Story.scss';

const Story = () => {
    return (
        <div className=" add-margin">
            <Title title="SUBMIT YOUR CONTENT" />
            <div className="container">
                <div className="row pt-3 pb-lg-5 pb-0">
                    <div className="col-lg-6">
                        <form className="contact__form__wrapper">
                            <div className="row">
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Content Title
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Content Format
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Content Genre
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Trailer Link
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                        placeholder="eg. google drive, we transfer etc"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Director Name
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Lead Actors
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Production House
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Email
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                                <div className="custom__form__group">
                                    <label
                                        htmlFor=""
                                        className="custom__form__label"
                                    >
                                        Contact Number
                                    </label>
                                    <input
                                        type="text"
                                        className="custom__form__input"
                                    />
                                </div>
                            </div>
                            <div className="contact__submit text-center pt-2">
                                <button
                                    type="submit"
                                    className="custom__btn"
                                >
                                    Submit
                                </button>
                            </div>
                        </form>
                    </div>
                    <div className="col-lg-6">
                        <div className="contact__img d-none d-lg-block">
                            <img className="img-fluid" src={image} alt="" />
                        </div>
                        <div className="mt-4">
                            <h3>Why Submit your Content?</h3>
                            <p className="pt-2">
                                Here’s some advice that many of our writers use to create saleable, appealing and worthwhile content.

                                Finding out what to write about:

                                Take a look at the “writing ideas” section to understand what customers are looking for including:
                                Catalog sales – What’s sold recently from the content catalog
                                Categories – A list of articles available by category
                                Search topics – What people are searching for on Constant Content
                                Review the “requested content” area and see if there are any custom requests you could write for

                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Story