import ReactImageAppear from 'react-image-appear';
import { Link, useHistory } from 'react-router-dom';
import './EventItem.scss';
import instance from '../../connection.js'

const EventItem = (props) => {
    const { upcoming } = props;
    const history = useHistory()
    const quickShowHandler = (event) => {
        event.preventDefault();
        history.push('/ticket/bariye-dao-tomar-hat')
    };
    return (
        <div>
            {upcoming &&
                <Link to={"/event-details/" + upcoming.event_slug} className="event__item">
                    <div className="event__item__img">
                        <ReactImageAppear className="img-fluid" src={instance+`/${upcoming.image}`} alt={upcoming.alt} />
                    </div>
                    <div className="event__item__content">
                        <h6 className="event__item__title pt-1 pb-1">
                            {upcoming.event_category}
                        </h6>
                        <div className="event__item__details pt-1 pb-1">
                            <h6 className="pt-1 pb-1 event-title">{upcoming.event_title}</h6>
                            <span className="event-artist-name">Artist: {upcoming.artist_name}</span>
                            <div className="text-center pb-2 pt-2">
                                <button
                                    type="button"
                                    className="event__item__btn remove_border"
                                    onClick={quickShowHandler}
                                >
                                    Buy Ticket
                                </button>
                            </div>
                        </div>
                    </div>
                </Link>}
        </div>
    );
};

export default EventItem;
