import { FaMoneyCheckAlt, FaRegCalendarAlt } from 'react-icons/all';
import './EventTitle.css';

const EventTitle = ({ title, type }) => {
    return (
        <div className="title">
            <h5
                className="title__name event__title"
                style={{ paddingLeft: '0' }}
            >
                {type === 1 && <FaMoneyCheckAlt className="price" />}
                {type === 2 && <FaRegCalendarAlt className="calender" />}
                {title}
            </h5>
            <hr className="title__line" />
        </div>
    );
};

export default EventTitle;
