import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import { CKEditor } from "@ckeditor/ckeditor5-react";
import Input from "../Input/Input";
import Select from "../Input/Select";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useAlert } from 'react-alert';
import { createEventActionsoffline } from "../../actions/creat_event_actions";


const OfflineForm = ({ query }) => {

    const alert = useAlert();

    const events = [
        { id: 1, value: 'Private Party', label: 'Private Party' },
        { id: 2, value: 'Concert/Festival', label: 'Concert/Festival' },
        { id: 3, value: 'Corporate', label: 'Corporate' },
        { id: 4, value: 'Campus', label: 'Campus' },
        { id: 5, value: 'Wedding', label: 'Wedding' },
        { id: 6, value: 'Video shootout', label: 'Video shootout' },
        { id: 7, value: 'Restaurant', label: 'Restaurant' },
        { id: 7, value: 'Inauguration', label: 'Inauguration' },
    ]

    const dispatch = useDispatch();
    const createEventData = useSelector((state) => state.getCreateEventoffline);
    var { create_event } = createEventData;

    const [event, setEvent] = useState('');
    const [artistName, setArtistName] = useState('');
    const [eventtime, setEventtime] = useState('');
    const [startDate, setStartDate] = useState('');
    const [endDate, setEndDate] = useState('');
    const [address, setAddress] = useState('');
    const [artistBudget, setartistBudget] = useState('');
    const [email, setEmail] = useState('');
    const [contactNumber, setContactNumber] = useState('');
    const [description, setDescription] = useState('');


    if (create_event) {
        if (create_event.affectedRows > 0) {
                // document.getElementById("eventName").value = "";
                // document.getElementById("startDate").value = "";
                // document.getElementById("endDate").value = "";
                // document.getElementById("genres").value = "";
                // document.getElementById("event_link").value = "";
                // document.getElementById("hear_about").value = "";
                // document.getElementById("email").value = "";
                // document.getElementById("contactNumber").value = "";
                // document.getElementById("platform").value = "";
                // document.getElementById("eventtime").value = "";

            alert.show("Your request is received. We'll contact with you soon.");
        }
    }

    function PostDataoffline() {
            dispatch(
                createEventActionsoffline(
                    event,
                    artistName,
                    eventtime,
                    startDate,
                    endDate,
                    address,
                    artistBudget,
                    email,
                    contactNumber,
                    description
                )
            );
    };



    return (
        <div className="form-wrapper">
            <div className="event-form contact__form__wrapper">
                <div className="row">
                    <div className="col-12">
                        <Select
                            label="event"
                            labelTitle="Event Genres/Type"
                            options={events}
                            required
                            onChange={(e) => {
                                setEvent(e.target.value)
                            }}
                        />
                    </div>
                    {query && (
                        <div className="col-12">
                            <Input
                                label="artistName"
                                labelTitle="Artist Name"
                                name="artistName"
                                required
                                onChange={(e) => {
                                    setArtistName(e.target.value)
                                }}
                            />
                        </div>
                    )}


                    <div className="col-12 col-md-6">
                        <Input
                            label="artistName"
                            labelTitle="Event Time"
                            name="artistName"
                            type="time"
                            required
                            onChange={(e) => {
                                setEventtime(e.target.value)
                            }}
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="startDate"
                            labelTitle="Start Date"
                            name="startDate"
                            type="date"
                            required
                            onChange={(e) => {
                                setStartDate(e.target.value)
                            }}
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="endDate"
                            labelTitle="End Date"
                            name="endDate"
                            type="date"
                            required
                            onChange={(e) => {
                                setEndDate(e.target.value)
                            }}
                        />
                    </div>

                    <div className="col-12 col-md-6">
                        <Input
                            label="address"
                            labelTitle="Venue Address"
                            name="address"
                            type="text"
                            required
                            onChange={(e) => {
                                setAddress(e.target.value)
                            }}
                        />
                    </div>

                    <div className="col-12 col-md-6">
                        <div className="custom__form__group">
                            <label htmlFor="" className="custom__form__label">
                                Artist Budget
                            </label>
                            <input
                                type="text"
                                name="artistBudget"
                                id="artistBudget"
                                placeholder=""
                                className="custom__form__input"
                                onChange={(e) => {
                                    setartistBudget(e.target.value)
                                }}
                            />
                        </div>
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="email"
                            labelTitle="Email Address"
                            name="email"
                            type="email"
                            required
                            onChange={(e) => {
                                setEmail(e.target.value)
                            }}
                        />
                    </div>
                    <div className="col-12 col-md-6">
                        <Input
                            label="contactNumber"
                            labelTitle="Contact Number"
                            name="contactNumber"
                            type="text"
                            required
                            onChange={(e) => {
                                setContactNumber(e.target.value)
                            }}
                        />
                    </div>


                    <div className="col-12 ">
                        <div className="custom__form__group">
                            <label htmlFor="" className="custom__form__label">
                                Requirement Details (Optional)
                            </label>
                            <CKEditor
                                className="height"
                                editor={ClassicEditor}
                                data={console.log()}
                                name="description"
                                onChange={(event, editor) => {
                                    setDescription(editor.getData());
                                }}
                                
                            />
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="custom__form__group text-center mt-2">
                            <button
                                type="submit"
                                className="custom__submit__btn"
                                onClick={PostDataoffline}
                            >
                                Submit
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default OfflineForm;
