import React, { useEffect } from "react";
import ReactOwlCarousel from "react-owl-carousel";
import { useDispatch, useSelector } from "react-redux";
import { getLimitedBlogs } from "../../actions/ticket_blogs_actions";
import { option } from "../Event/Option";
import "./News.scss";
import NewsItem from "./NewsItem";



const News = () => {
    const dispatch = useDispatch();
    const blog_list = useSelector((state) => state.getlimitedBlogs);
    const { loading, error, limited_blogs } = blog_list;

    useEffect(() => {
        dispatch(getLimitedBlogs());
    }, []);


    return (
        <div className="news">
            <div className="container navs-right  event-wrapper">
                {limited_blogs && (
                    <ReactOwlCarousel className="owl-theme " {...option}>
                        {limited_blogs.map((limited_blogs) => (
                            <div key={limited_blogs.blog_id}>
                                <NewsItem limited_blogs={limited_blogs} />
                            </div>
                        ))}
                    </ReactOwlCarousel>
                )}
            </div>
        </div>
    );
};

export default News;
