import { FaArrowDown } from 'react-icons/fa';
import bankCard from '../../assets/images/bank-building.png';
import cardImage from '../../assets/images/credit-card.png';
import paypalImage from '../../assets/images/paypal.png';
import Axios from 'axios';
import CartStore from '../../cartstore.js';
import instance from '../../connection.js'

const PaymentMethod = ({ setStep }) => {
    function makepayment() {
        Axios.post(instance+`/payment/pay`, { currency: CartStore.getpaymentcurrency(), total: CartStore.getTotalprice(), name: CartStore.getfullname() })
            .then((res) => {
                var url = res.data.GatewayPageURL;
                window.location.href = url;
            })
    }
    return (
        <div className="ticketForm">
            <div className="custom__form__group">
                <h5>Payment</h5>
            </div>
            <div className="payment__method">
                <div className="payment__method__img">
                    <img src={paypalImage} alt="" />
                </div>
                <div className="payment__method__name">
                    <h4>Paypal</h4>
                </div>
                <div className="payment__method__down">
                    <FaArrowDown />
                </div>
            </div>
            <div className="payment__method">
                <div className="payment__method__img">
                    <img src={cardImage} alt="" />
                </div>
                <div className="payment__method__name">
                    <h4>DEBIT CARD</h4>
                    <span>
                        Issued only in BD. For cards issued outside BD, select
                        International Cards
                    </span>
                </div>
                <div className="payment__method__down">
                    <FaArrowDown />
                </div>
            </div>
            <div className="payment__method">
                <div className="payment__method__img">
                    <img src={cardImage} alt="" />
                </div>
                <div className="payment__method__name">
                    <h4>CREDIT CARD</h4>
                    <span>
                        Issued only in BD. For cards issued outside BD, select
                        International Cards
                    </span>
                </div>
                <div className="payment__method__down">
                    <FaArrowDown />
                </div>
            </div>
            <div className="payment__method">
                <div className="payment__method__img">
                    <img src={bankCard} alt="" />
                </div>
                <div className="payment__method__name">
                    <h4>E-BANKING</h4>
                    <span>Bkash, Nagad, Rocket, U-Cash etc</span>
                </div>
                <div className="payment__method__down">
                    <FaArrowDown />
                </div>
            </div>
            <div className="payment__method">
                <div className="payment__method__img">
                    <img src={cardImage} alt="" />
                </div>
                <div className="payment__method__name">
                    <h4>INTERNATIONAL CARDS</h4>
                </div>
                <div className="payment__method__down">
                    <FaArrowDown />
                </div>
            </div>
            <div className="custom__form__group text-center  d-flex d-sm-block justify-content-center ">
                <button className="custom__submit__btn mt-2 mt-sm-2 mt-md-3 me-3" onClick={() => setStep(2)} >Previous</button>
                <button className="custom__submit__btn mt-2 mt-sm-2 mt-md-3" onClick={() => makepayment()} >Continue</button>
            </div>
        </div>
    );
};

export default PaymentMethod;
