import { applyMiddleware, combineReducers, compose, createStore } from 'redux';
import thunk from 'redux-thunk';
import { artistDetailsReducers, artistReducers } from '../reducers/artists_reducers';
import { createEventReducers,createEventReducersOffline } from '../reducers/creat_event_reducers';
import { eventDetailsReducers } from '../reducers/event_details_reducers';
import { eventRequestReducers } from '../reducers/event_requests_reducers';
import { getHowtobuyTicketReducer, howToHostYourShowListReducer } from '../reducers/How_to_host_your_shows_reducer';
import { loginReducers, registerReducers } from '../reducers/login_register_reducers';
import { metaListReducer } from '../reducers/meta_tag_reducer';
import { allBlogsReducers, blogDetailsReducer, limitedBlogsReducers } from '../reducers/ticker_blogs_reducer';
import { sliderListReducer } from '../reducers/ticket_home_slider_reducer';
import { infoReducer } from '../reducers/ticket_info_reducers';
import { likeRequestReduces } from '../reducers/like_reducers';
import { allEventsReducer, upcomingEventsReducer } from '../reducers/upcoming_events_reducers';

const initialState = {};
const reducer = combineReducers({
    sliderList: sliderListReducer,
    getinfo: infoReducer,
    getMeta: metaListReducer,
    how_to_host: howToHostYourShowListReducer,
    how_to_buy: getHowtobuyTicketReducer,
    getBlogs: allBlogsReducers,
    getlimitedBlogs: limitedBlogsReducers,
    getblogDetails: blogDetailsReducer,
    getUpcomingEvents: upcomingEventsReducer,
    getEventDetails: eventDetailsReducers,
    getAllEvents: allEventsReducer,
    getregister: registerReducers,
    getlogin: loginReducers,
    getartist: artistReducers,
    getartistDetails: artistDetailsReducers,
    getCreateEvent: createEventReducers,
    getCreateEventoffline: createEventReducersOffline,
    getEventRequest: eventRequestReducers,
    getlikeRequestReduces:likeRequestReduces
})
const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducer, initialState, composeEnhancer(applyMiddleware(thunk)));

export default store;