import React from 'react';
import { AiOutlineCalendar } from 'react-icons/ai';
import { BiTimeFive } from 'react-icons/bi';
import { FaPlay } from 'react-icons/fa';
import { HiOutlineCurrencyBangladeshi } from 'react-icons/hi';
import { Link } from 'react-router-dom';
import image from '../../../assets/images/reunion.png';
import instance from '../../../connection.js'

const SliderItem = (props) => {
    const { slider } = props;
    return (
        <div>
        {slider && (
        <div className="banner" >
            <div className="container banner-container">
                <div className="row">
                    <div className="col-md-3 d-none d-lg-block">
                        <div className="banner-side-image  ">
                            <img src={instance+`/${slider.image}`} alt={slider.alt}/>        </div>
                    </div>
                    <div className="col-md-9">
                        <div className="home-slider-content">
                            <h1 className="banner-title">
                               {slider.text}
                            </h1>
                            <ul className="labels">
                                <li className="label-item">Science fiction </li>
                                <li className="label-item">Drama  </li>
                                <li className="label-item"> Action  </li>
                            </ul>
                            <div className="description">
                            {slider.description}
                            </div>
                            <div className="info">
                                <div className="info-item">
                                    <BiTimeFive />  {slider.duration}
                                </div>
                                <div className="info-item">
                                    <AiOutlineCalendar />  {slider.year}
                                </div>
                                <div className="info-item">
                                    <HiOutlineCurrencyBangladeshi style={{ marginTop: '-3px' }} />   {slider.price}
                                </div>
                            </div>

                            <div className="slider-buttons">
                                <Link to="/video/jungle" className="slider-button">
                                    <FaPlay /> Watch Trailer
                                </Link>
                                <button className="slider-button ms-5">
                                    Buy Now
                                </button>
                            </div>
                            <button className="slider-button mt-3  d-md-none ">
                                Buy Now
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )}
        </div>
    );
};

export default SliderItem;