import React, { useEffect } from "react";
import { Helmet } from "react-helmet";
import { useDispatch, useSelector } from "react-redux";
import { getMeta } from "../../actions/meta_tag_actions";

export default function Metatag(props) {
  const { metatag_name, info } = props;
  const dispatch = useDispatch();
  const gemeta = useSelector((state) => state.getMeta);
  const { loading_meta, error_meta, metatag } = gemeta;
  useEffect(() => {
    dispatch(getMeta(metatag_name));
  }, []);
  return (
    <div>
      {metatag ? (
        <div>
          {metatag.map((metatag) => (
            <Helmet key={metatag.meta_id}>
              <link rel="icon" href={`/${metatag.meta_fav}`} />
              <link rel="icon" href={`/${metatag.meta_fav}`} />
              <title>{metatag.meta_title}</title>
              <meta name="description" content={metatag.meta_description} />

            </Helmet>
          ))}
        </div>
      ) : (
        <div></div>
      )}
    </div>
  );
}
