import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getLimitedBlogs } from "../../actions/ticket_blogs_actions";
import NewsItem from '../News/NewsItem';
import Title from '../Shared/Title/Title';

const Blogs = () => {
    const dispatch = useDispatch();
    const blog_list = useSelector((state) => state.getlimitedBlogs);
    const { loading, error, limited_blogs } = blog_list;
    useEffect(() => {
        dispatch(getLimitedBlogs());
    }, []);
    return (
        <div className="add-margin">
            <Title title="BLOG" />
            {limited_blogs ? (
                <div className="container">
                    <div className="row g-2 g-sm-3">
                        {limited_blogs.map((limited_blogs) => (
                            <div key={limited_blogs.blog_id} className=" col-6 col-sm-6 col-md-4 col-lg-3">
                                <NewsItem limited_blogs={limited_blogs} />
                            </div>
                        ))}
                    </div>
                </div>
            ) : (
                <div></div>
            )}
        </div>
    );
};

export default Blogs;
