import React, { useEffect, useState } from "react";
import { FaFacebookF, FaInstagram, FaTwitter } from "react-icons/fa";
import ReactImageAppear from "react-image-appear";
import ReactOwlCarousel from "react-owl-carousel";
import { useDispatch, useSelector } from "react-redux";
import renderHTML from 'react-render-html';
import { useParams } from "react-router";
import { Link } from "react-router-dom";
import { artistAction, artistDetailsAction } from "../../actions/artists_actions";
import backgroundImage from '../../assets/images/background.png';
import HireArtists from "../Pages/Home/HireArtists";
import Title from "../Shared/Title/Title";
import Video from "../Video/profile_video";
import "./Profile.scss";
import { FaHeart } from 'react-icons/fa';
import { likeartistaction } from "../../actions/like_actions";
import instance from "../../connection";

const ProfileDetails = () => {
    const { slug } = useParams();
    const dispatch = useDispatch();
    const [follow, setFollow] = useState(false);
    
    function setLike(){
        dispatch(
            likeartistaction(
                slug
            )
        );
    }

    const artist_details_Data = useSelector((state) => state.getartistDetails);
    const { artists_details } = artist_details_Data;

    const artists_data = useSelector((state) => state.getartist);
    const { artists } = artists_data;
    useEffect(() => {
        dispatch(artistAction());
    }, [dispatch]);

    useEffect(() => {
        dispatch(artistDetailsAction(slug));
    }, []);


    const option = {
        loop: false,
        margin: 10,
        nav: true,
        responsiveClass: true,
        autoplay: true,
        dots: false,
        items: 1,
        navText: [
            '<svg class="svg-icon" stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 320 512" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M34.52 239.03L228.87 44.69c9.37-9.37 24.57-9.37 33.94 0l22.67 22.67c9.36 9.36 9.37 24.52.04 33.9L131.49 256l154.02 154.75c9.34 9.38 9.32 24.54-.04 33.9l-22.67 22.67c-9.37 9.37-24.57 9.37-33.94 0L34.52 272.97c-9.37-9.37-9.37-24.57 0-33.94z"></path></svg>',
            '<svg class="svg-icon" stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 320 512" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M285.476 272.971L91.132 467.314c-9.373 9.373-24.569 9.373-33.941 0l-22.667-22.667c-9.357-9.357-9.375-24.522-.04-33.901L188.505 256 34.484 101.255c-9.335-9.379-9.317-24.544.04-33.901l22.667-22.667c9.373-9.373 24.569-9.373 33.941 0L285.475 239.03c9.373 9.372 9.373 24.568.001 33.941z"></path></svg>',
        ],
    };

    return (
        <div className="add-margin">
            {artists_details ? (
                <div className="profile-details">
                    {artists_details.map((artists_details) => (
                        <div key={artists_details.artist_id}>
                            <Title title={artists_details.artist_name} titleNumber={3} />

                            <div className="container">
                                <div className="profile__details__wrapper">
                                    <div className="profile__banner">
                                        {/* *********** Cover image ************ */}
                                        <ReactOwlCarousel className="owl-theme" {...option}>
                                            <img src={backgroundImage}
                                                alt={artists_details.artist_alt}
                                                className="profile__banner__img img-fluid" />
                                            <img src={backgroundImage}
                                                alt={artists_details.artist_alt}
                                                className="profile__banner__img img-fluid" />
                                            <img src={backgroundImage}
                                                alt={artists_details.artist_alt}
                                                className="profile__banner__img img-fluid" />
                                            <img src={backgroundImage}
                                                alt={artists_details.artist_alt}
                                                className="profile__banner__img img-fluid" />

                                        </ReactOwlCarousel>


                                        <hr />
                                        {/* ********* profile image ********* */}
                                        <div className="profile__image">
                                            <ReactImageAppear
                                                src={instance+`/${artists_details.artist_image}`}
                                                alt={artists_details.artist_alt}
                                            />
                                        </div>
                                        {/* *********** Social Icons *********** */}
                                        <div className="profile__social">
                                            <Link
                                                to={{ pathname: artists_details.fb }}
                                                className="profile__social__icon"
                                                target="_blank"
                                            >
                                                <FaFacebookF />
                                            </Link>
                                            <Link
                                                to={{ pathname: artists_details.twitter }}
                                                className="profile__social__icon"
                                                target="_blank"
                                            >
                                                <FaTwitter />
                                            </Link>
                                            <Link
                                                to={{ pathname: artists_details.instagram }}
                                                className="profile__social__icon"
                                                target="_blank"
                                            >
                                                <FaInstagram />
                                            </Link>

                                        </div>
                                    </div>
                                    <div className="text-end mb-3">
                                    <h5 className="mt-1">  </h5>
                                        <div onClick={() => setLike()} className="btn btn-danger btn-sm"><FaHeart className="heart__icon" /> {artists_details.heart} </div>
                                        <Link className="btn btn-danger btn-sm ms-3 text-white" to={`/create-event?name=${slug}`}>Hire Me</Link>
                                    </div>
                                    <div className="row">
                                        <div className="col-lg-4">

                                            <Video video_id={artists_details.promo_video} />
                                        </div>
                                        <div className=" col-lg-8">
                                            <div className="about__content">
                                                {renderHTML(artists_details.details)}
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            ) : (
                <div></div>
            )}

            <div className="home__section pt-3">
                <Title
                    title="Related ARTIST "
                    link="/artists"
                    titleNumber={1}
                />
                <HireArtists artists={artists} />
            </div>
        </div>
    );
};

export default ProfileDetails;
