import React, { useEffect, useState } from "react";
import { MultiStepForm, Step } from 'react-multi-form';
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { eventDetailsActions } from "../../actions/event_details_actions";
import Success from "../Shared/Success/Success";
import Title from '../Shared/Title/Title';
import PaymentMethod from './PaymentMethod';
import TicketForm from './TicketForm';
import TicketForm2 from './TicketForm2';
import './TicketSteps.css';


const TicketSteps = () => {
    const [step, setStep] = useState(1);
    const { slug } = useParams();
    const dispatch = useDispatch();
    const details = useSelector((state) => state.getEventDetails);
    const { event_details } = details;
    useEffect(() => {
        dispatch(eventDetailsActions(slug));
    }, [dispatch, slug]);

    return (
        <div className="add-margin mt-3">
            <Title title="TICKET BOOKING PROCEDURE" titleNumber={3} />
            <div className="container push-bottom">
                <div className="col-md-8 col-lg-6 col-11 mx-auto mt-4">
                    <MultiStepForm accentColor="#ed1b28" activeStep={step}>
                        <Step label="Basic Info">
                            <TicketForm setStep={setStep} event_details={event_details} />
                        </Step>
                        <Step label="Ticketing and Pricing">
                            <TicketForm2 setStep={setStep} event_details={event_details} />
                        </Step>
                        <Step label="Payment">
                            <PaymentMethod setStep={setStep} event_details={event_details} />
                        </Step>
                    </MultiStepForm>
                    {step === 4 && <Success message="Thanks your payment" />}
                </div>
            </div>
        </div>
    );
};

export default TicketSteps;
