import {INFO_LIST_REQUEST,INFO_LIST_SUCCESS,INFO_LIST_FAIL} from '../constants/ticket_infoConstants';

export const infoReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case INFO_LIST_REQUEST:
            return {loading:true};

            case INFO_LIST_SUCCESS:
                return {loading:false, info: action.payload};

                case INFO_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}