import React from 'react';
import { AiOutlineUser } from 'react-icons/ai';
import { BsCheckCircle } from 'react-icons/bs';
import { FaRegCreditCard, FaWallet } from 'react-icons/fa';
import { FiLogOut } from 'react-icons/fi';
import { ImTicket } from 'react-icons/im';
import { MdSubscriptions } from 'react-icons/md';
import { RiLockPasswordLine, RiUserHeartLine } from 'react-icons/ri';
import { SiOpenaccess } from 'react-icons/si';
import { NavLink } from 'react-router-dom';
import image from '../../assets/images/avatar.png';

const ProfileRoutes = () => {
    return (
        <div className="profile-menu shadow-sm">
            <div className="profile__user">
                <img
                    className="img-fluid profile__user__img"
                    src={image}
                    alt="Profile"
                />
                <h3 className="profile__user__name">
                    {`Sujon Hossain`}
                    <BsCheckCircle className="profile__check__success" />
                </h3>
            </div>
            {/* ********* Route ********** */}
            <NavLink
                exact
                to="/user-profile/"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="dashboard">
                    <span className="profile-icon">
                        <AiOutlineUser />
                    </span>
                    <span className="profile-route">Dashboard</span>
                </label>
            </NavLink>

            <NavLink
                to="/user-profile/subscriptions"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="subscriptions">
                    <span className="profile-icon">
                        <MdSubscriptions />
                    </span>
                    <span className="profile-route">My Subscriptions</span>
                </label>
            </NavLink>
            {/* ********* Route ********** */}
            <NavLink
                to="/user-profile/tickets"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="tickets">
                    <span className="profile-icon">
                        <ImTicket />
                    </span>
                    <span className="profile-route"> My All Tickets </span>
                </label>
            </NavLink>
            {/* ********* Route ********** */}
            <NavLink
                to="/user-profile/wallet"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="wallet">
                    <span className="profile-icon">
                        <FaWallet />
                    </span>
                    <span className="profile-route"> My All Wallet </span>
                </label>
            </NavLink>
            <NavLink
                to="/user-profile/access-code"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="access">
                    <span className="profile-icon">
                        <SiOpenaccess />
                    </span>
                    <span className="profile-route"> Access Code </span>
                </label>
            </NavLink>

            {/* <NavLink
                to="/user-profile/reviews"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="reviews">
                    <span className="profile-icon">
                        <VscOpenPreview />
                    </span>
                    <span className="profile-route"> Reviews </span>
                </label>
            </NavLink> */}
            <NavLink
                to="/user-profile/transaction"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="transaction">
                    <span className="profile-icon">
                        <FaRegCreditCard />
                    </span>
                    <span className="profile-route">Transaction</span>
                </label>
            </NavLink>
            <NavLink
                to="/user-profile/artist-portfolio"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="transaction">
                    <span className="profile-icon">
                        <RiUserHeartLine />
                    </span>
                    <span className="profile-route">Artist Portfolio</span>
                </label>
            </NavLink>
            <NavLink
                to="/user-profile/change-password"
                className="profile-menu-item"
                activeClassName="profile-active"
            >
                <label htmlFor="transaction">
                    <span className="profile-icon">
                        <RiLockPasswordLine />
                    </span>
                    <span className="profile-route">Change Password</span>
                </label>
            </NavLink>
            <div className="profile-menu-item">
                <label htmlFor="logout">
                    <span className="profile-icon">
                        <FiLogOut />
                    </span>
                    <span className="profile-route">logout</span>
                </label>
            </div>
        </div>
    );
};

export default ProfileRoutes;
