import { BiDevices, MdDesktopWindows, VscPreview } from 'react-icons/all';
import { useHistory } from 'react-router-dom';

const PackageItem = () => {
    const history = useHistory();
    return (
        <div className="vip-item">
            <p>1ST MONTH FREE TRIAL </p>
            <div className="subs"> Standard Monthly Subscription </div>
            <div className="price">
                <span className="one">$0.00 </span>
                <span className="two">$3.9</span>
            </div>
            <div className="auto__renew">
                Next renewal will cost $3.99
                <br />
                Auto renew 1 month upon expiry.
                <br />
                Cancel anytime
                <br />
            </div>
            <hr />
            <div className="show">
                <div className="show__item">
                    <BiDevices />
                    <p>2 Screens</p>
                </div>
                <div className="show__item">
                    <MdDesktopWindows />
                    <p>Full HD 1080P</p>
                </div>
                <div className="show__item">
                    <VscPreview />
                    <p>Advanced viewing</p>
                </div>
            </div>
            <div className="mt-2 text-end">
                <button type="button" class="btn btn-outline-danger btn-sm ">Buy Now</button>
            </div>
        </div>
    );
};

export default PackageItem;
