import {EVENT_REQUEST_REQUEST,EVENT_REQUEST_SUCCESS,EVENT_REQUEST_FAIL} from '../constants/event_request';

export const eventRequestReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case EVENT_REQUEST_REQUEST:
            return {loading:true};

            case EVENT_REQUEST_SUCCESS:
                return {loading:false, event_request: action.payload};

                case EVENT_REQUEST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}
