import { BiEditAlt } from 'react-icons/bi';
import { BsEyeFill } from 'react-icons/bs';
import { RiDeleteBin5Line } from 'react-icons/ri';
import AddBuyerQns from '../Modal/AddBuyerQns';
import AddressModal from '../Modal/AddressModal';
import AttendeeNameModal from '../Modal/AttendeeNameModal';
import EmailModal from '../Modal/EmailModal';
import MobileModal from '../Modal/MobileModal';
import NameModal from '../Modal/NameModal';

const CheckoutForm = () => {
    return (
        <section className="checkout__form">
            <h2 className="mb-2">Global checkout form</h2>
            <p>
                Customise the questions that your customers get asked when
                making an order. You can also override this message for
                particular events if you wish by going to
            </p>
            <p>
                <strong> Events </strong> {'>'} <strong> Your Event </strong>
                {' > '} <strong> Edit checkout </strong> form editing the
                checkout form from there.
            </p>

            <div className="checkout__form__box">
                <div className="checkout__form__header">
                    <h4>Buyer only questions</h4>
                    <div className="checkout__action__btn">
                        <button type="button" className="action__btn">
                            Add a buyer only question
                        </button>
                    </div>
                </div>
                <p>Buyer questions are asked once per order.</p>
                <div className="checkout__field">
                    <ul className="checkout__field__section">
                        <li className="checkout__field__element">
                            <span className="checkout__field__element-left">
                                Name *
                            </span>
                            <div className="checkout__field__element-right">
                                <BiEditAlt
                                    className="checkout__icon checkout__icon-edit"
                                    data-bs-toggle="modal"
                                    data-bs-target="#nameModal"
                                />
                                <RiDeleteBin5Line className="checkout__icon checkout__icon-delete" />
                            </div>
                        </li>
                        <li className="checkout__field__element">
                            <span className="checkout__field__element-left">
                                Email *
                            </span>
                            <div className="checkout__field__element-right">
                                <BiEditAlt
                                    className="checkout__icon checkout__icon-edit"
                                    data-bs-toggle="modal"
                                    data-bs-target="#emailModal"
                                />
                                <RiDeleteBin5Line className="checkout__icon checkout__icon-delete" />
                            </div>
                        </li>
                        <li className="checkout__field__element">
                            <span className="checkout__field__element-left">
                                Mobile Phone Number ( <strong>Hidden</strong> )
                            </span>
                            <div className="checkout__field__element-right ">
                                <BsEyeFill
                                    className="checkout__icon checkout__icon-edit"
                                    data-bs-toggle="modal"
                                    data-bs-target="#mobileModal"
                                />
                            </div>
                        </li>
                        <li className="checkout__field__element">
                            <span className="checkout__field__element-left">
                                Address ( <strong> Hidden </strong> )
                            </span>
                            <div className="checkout__field__element-right">
                                <BsEyeFill
                                    className="checkout__icon checkout__icon-edit"
                                    data-bs-toggle="modal"
                                    data-bs-target="#addressModal"
                                />
                            </div>
                        </li>
                    </ul>
                </div>
            </div>

            <div className="checkout__form__box  mt-lg-5 mt-sm-3 ">
                <div className="checkout__form__header">
                    <h4>Attendee questions </h4>
                    <div className="checkout__action__btn">
                        <button type="button" className="action__btn">
                            Add a Attendee only question
                        </button>
                    </div>
                </div>
                <p>Attendee questions are asked for every ticket purchased.</p>
                <div className="checkout__field">
                    <ul className="checkout__field__section">
                        <li className="checkout__field__element">
                            <span className="checkout__field__element-left">
                                Attendee name ( <strong> Hidden </strong> )
                            </span>
                            <div className="checkout__field__element-right">
                                <BsEyeFill
                                    className="checkout__icon checkout__icon-edit"
                                    data-bs-toggle="modal"
                                    data-bs-target="#attendeeName"
                                />
                            </div>
                        </li>
                    </ul>
                </div>
            </div>

            <AddBuyerQns />
            <NameModal />
            <EmailModal />
            <MobileModal />
            <AddressModal />
            <AttendeeNameModal />
        </section>
    );
};

export default CheckoutForm;
