import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { eventRequestActions } from '../../actions/event_request_actions';
import './settings.css';

const Home = () => {
    const [showMail, setShowMail] = useState(false);
    const dispatch = useDispatch();
    const event_request_data = useSelector((state) => state.getEventRequest);
    const { loading, error, event_request } = event_request_data;
    useEffect(() => {
        dispatch(eventRequestActions());
    }, []);
    var count = 0;

    return (
        <section className="basic__setting">
            <h4 className="mb-2">Event Request</h4>
            <div className="table-responsive">
                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col">#</th>
                            <th scope="col">Event Name</th>
                            <th scope="col">Start Date</th>
                            <th scope="col">Genres</th>
                            <th scope="col">Event Link</th>
                            <th scope="col">Hear About</th>
                            <th scope="col">Description</th>
                        </tr>
                    </thead>
                    {event_request ? (
                        <tbody>
                            {event_request.map((event_request) => (
                                <tr key={event_request.event_req_id}>
                                    <th scope="row">{++count}</th>
                                    <td>{event_request.event_name}</td>
                                    <td>{event_request.start_date}</td>
                                    <td>{event_request.genres}</td>
                                    <td>
                                        <Link
                                            target="_blank"
                                            to={{
                                                pathname:
                                                    event_request.event_link,
                                            }}
                                        >
                                            {event_request.event_link}
                                        </Link>
                                    </td>
                                    <td>{event_request.hear_about}</td>
                                    <td>
                                        <div
                                            dangerouslySetInnerHTML={{
                                                __html: event_request.description,
                                            }}
                                        />
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    ) : (
                        <div></div>
                    )}
                </table>
            </div>
        </section>
    );
};

export default Home;
