import { useEffect, useState } from 'react';
import Axios from 'axios';
import { option } from '../Event/Option';
import CartStore from '../../cartstore';


const TicketForm2 = (props) => {
    const { event_details, setStep } = props;
    const [info, setInfo] = useState([]);
    const [input, setInput] = useState(0);
    const [from, setFrom] = useState("usd");
    const [to, setTo] = useState("bdt");
    const [options, setOptions] = useState([]);
    const [output, setOutput] = useState(0);

    // Calling the api whenever the dependency changes
    useEffect(() => {
        Axios.get(
            `https://cdn.jsdelivr.net/gh/fawazahmed0/currency-api@1/latest/currencies/${from}.json`)
            .then((res) => {
                setInfo(res.data[from]);
            })
    }, [from]);



    // Calling the convert function whenever
    // a user switches the currency
    useEffect(() => {
        setOptions(Object.keys(info));
        convert();
    }, [info])
    function convert(price,to_curr) {
        var rate = info[to_curr];
        console.log(rate);
        console.log(price);
        setOutput((price  * rate).toFixed(2));
        return (price*rate).toFixed(2);
    }
    function updateprice(val) {
        const select_price = document.getElementById("select_price").value;
        var price = document.getElementById('mrp').value;
        setInput(price);
        price = convert(price,select_price);
        
        var qty = document.getElementById("qty").value;
        if (!qty) {
            qty = 1;
        }
        console.log(output);
        const price_placeholder = document.getElementById("price_placeholder").value = qty + "*" + price;
        document.getElementById("total_price").value = Number(price) * Number(qty);

        CartStore.setpaymentcurrency(select_price);
        CartStore.setTotalprice(Number(price) * Number(qty));

    }
    return (
        <div className="ticketForm">
            {event_details ? (
                <div>
                    <div className="custom__form__group">
                        <label htmlFor="" className="custom__form__label">
                            Event Name
                        </label>
                        <input
                            type="text"
                            name=""
                            placeholder="Gane Gane Miluk Pran"
                            className="custom__form__input"
                            value={event_details[0].event_title}
                            onKeyUp={CartStore.setevent(event_details[0].event_title,event_details[0].event_id)}
                        />
                    </div>
                    <div className="custom__form__group">
                        <label htmlFor="" className="custom__form__label">
                            Payment Currency
                        </label>
                        <input type='hidden' name="mrp" id="mrp" value={event_details[0].event_price} />
                        <select name="" id="select_price" className="custom__form__input form-field-name"  onChange={(e) => {updateprice(e.value)}}>
                            <option value="">--Select--</option>
                            {options.map((options) => (
                            <option  value={options}>{options.toUpperCase()} </option>
                            ))}
                        </select>
                    </div>
                    <div className="row p-0">
                        <div className="col-lg-6">
                            <div className="custom__form__group">
                                <label htmlFor="" className="custom__form__label">
                                    Price
                                </label>
                                <input
                                    type="text"
                                    name=""
                                    id="price_placeholder"
                                    placeholder="1 * 120"
                                    className="custom__form__input"
                                />
                            </div>
                        </div>
                        <div className="col-lg-6">
                            <div className="custom__form__group">
                                <label htmlFor="" className="custom__form__label">
                                    Total Price
                                </label>
                                <input
                                    type="text"
                                    name=""
                                    id="total_price"
                                    placeholder="120 TK"
                                    className="custom__form__input"
                                />
                            </div>
                        </div>
                    </div>
                    {/*<div className="custom__form__group">
                <h6>
                    Total Price:
                    <span id="total_price_text"> 120 Tk</span>
                </h6>
            </div> */}
                    <div className=" d-flex align-items-center">
                        <input type="checkbox" name="" />
                        <label htmlFor="" className="custom__form__label mb-0 ms-2">
                            Have a Coupon Code ??
                        </label>
                    </div>
                    <div className="custom__form__group d-flex px-2">
                        <input type="text" name="" className="custom__form__input" />

                        <button className="btn btn-dark btn-sm ms-2" height="30px">
                            Apply
                        </button>
                    </div>
                </div>
            ) : (
                <div></div>
            )}
            <div className="custom__form__group text-center  d-flex d-sm-block justify-content-center ">
                <button className="custom__submit__btn mt-1 mt-sm-2 mt-md-3 me-3" onClick={() => setStep(1)} >Previous</button>
                <button className="custom__submit__btn mt-1 mt-sm-2 mt-md-3" onClick={() => setStep(3)} >Continue</button>
            </div>
        </div>
    );
};

export default TicketForm2;
