import {HOW_TO_HOST_LIST_REQUEST,HOW_TO_HOST_LIST_SUCCESS,HOW_TO_HOST_LIST_FAIL, HOW_TO_BUY_TICKET_LIST_REQUEST, HOW_TO_BUY_TICKET_SUCCESS, HOW_TO_BUY_TICKET_FAIL} from '../constants/How_to_host_your_show_constants';
import Axios from 'axios';
import instance from '../connection.js'

export const getHowtohostyourshows = () => async(dispactch)=>{
    dispactch({
        type:HOW_TO_HOST_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/how-to-host');
        dispactch({type: HOW_TO_HOST_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:HOW_TO_HOST_LIST_FAIL,payload: error.message});
    }
}

export const getHowtobuyTicket = () => async(dispactch)=>{
    dispactch({
        type:HOW_TO_BUY_TICKET_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/how-to-buy-ticket');
        dispactch({type: HOW_TO_BUY_TICKET_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:HOW_TO_BUY_TICKET_FAIL,payload: error.message});
    }
}
