import React, { useEffect, useState } from 'react';
import { AiOutlineUser } from 'react-icons/ai';
import { IoSearch, RiGlobalLine, RiVipCrown2Line } from 'react-icons/all';
import { GoSearch } from 'react-icons/go';
import { Link, useHistory, useLocation } from 'react-router-dom';
import logo from '../../../assets/images/Logo.png';
import VipModal from '../../Vip/VipModal';
import './Header.scss';
import auth from "../../../auth_handler";

import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

function Header() {
    const { pathname } = useLocation();
    const history = useHistory();
    const [modalIsOpen, setIsOpen] = useState(false);

    const [myOptions, setMyOptions] = useState([])
  
    const getDataFromAPI = () => {
      console.log("Options Fetched from API")
    
      fetch('http://dummy.restapiexample.com/api/v1/employees').then((response) => {
        return response.json()
      }).then((res) => {
        console.log(res.data)
        for (var i = 0; i < res.data.length; i++) {
          myOptions.push(res.data[i].employee_name)
        }
        setMyOptions(myOptions)
      })
    }

    useEffect(() => {
        const nav = document.querySelector('.navbar');
        function fixNav() {
            if (window.scrollY >= 50) {
                nav.classList.add('fixed');
            } else {
                nav.classList.remove('fixed');
            }
        }
        window.addEventListener('scroll', fixNav);
    }, []);



    return (
        <>
            <header>
                <nav className={`${pathname !== '/' && 'fixed2'} navbar navbar-expand-lg navbar-light sticky`}  >
                    <div className="container">
                        <Link className="navbar-brand" to="/">
                            <img className="img-fluid" src={logo} alt="Sujon Hossain" />
                        </Link>
                        <Link to="/search"
                            className="navbar-toggler"
                            type="button"
                        >
                            <GoSearch className="toggle__bar" />
                        </Link>
                        <div className="collapse navbar-collapse" id="navbarMenu">
                            <ul className="navbar-nav me-auto">
                                <li className="nav-item">
                                    <Link className="nav-link" to="/create-event">
                                        Create Event
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/artist-portfolio">
                                        Artist Portfolio
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/buy-ticket" >
                                        Buy Tickets
                                    </Link>
                                </li>

                                <li className="nav-item">
                                    <Link className="nav-link" to="/find-token">
                                        Find My Ticket
                                    </Link>
                                </li>
                            </ul>
                            <ul className="navbar-nav ms-auto">
                                <li className="nav-item">
                                    <div className="searchBoxWrapper">
                                        <div className="searchBox">
                                            <div className="searchForm">
                                                <input
                                                    type="text"
                                                    name=""
                                                    id=""
                                                    onKeyDown={getDataFromAPI}
                                                    placeholder="Search Movies"
                                                />
                                                <div className="searchBtn">
                                                    <IoSearch />
                                                    <div className="line" />
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </li>

                                <li className="nav-item">
                                    <div className="navLinkContainer">
                                        <div className="iconWrapper">
                                            <RiGlobalLine />
                                        </div>
                                        <div className="subMenuBox">
                                            <div className="subMenuBoxContainer">
                                                <div className="subMenuBoxListWrapper">
                                                    <ul className="languageList">
                                                        <li className="languageActive">English</li>
                                                        <li>Bangla</li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </li>

                                <li className="nav-item">
                                    <div className="navLinkContainer">
                                        <div className="iconWrapper">
                                            <AiOutlineUser />
                                        </div>
                                        <div className="subMenuBox">
                                            <div className="subMenuBoxContainer">
                                                <div className="subMenuBoxListWrapper">
                                                    {!auth.isAuthenticated() && 
                                                    <div className="loginBox">
                                                        <p>Login to watch trendy content</p>
                                                        <button onClick={() => history.push('/login')} type="button" className="loginBtn">
                                                            Login
                                                        </button>
                                                    </div> }
                                                    {auth.isAuthenticated() && 
                                                    <div className="loginBox">
                                                        <p className='text-center'>{auth.getname()}</p>
                                                        <button onClick={() => {
          auth.logout(() => {
            history.push("/login");
          });
        }} type="button" className="loginBtn">
                                                            Logout
                                                        </button>
                                                    </div> }
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </li>
                                <li className="nav-item">
                                    <div className="vipTag" style={{ cursor: 'pointer' }} role="button" onClick={() => setIsOpen(true)}>
                                        <div className="userLevelTag" >
                                            <RiVipCrown2Line />
                                            PACKAGE
                                        </div>
                                    </div>
                                </li>
                                <VipModal modalIsOpen={modalIsOpen} setIsOpen={setIsOpen} />
                            </ul>
                        </div>
                    </div>
                </nav>
            </header>
        </>
    );
}

export default Header;
