import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Slider from "react-slick";
import { artistAction } from "../../../actions/artists_actions";
import { listSliders } from "../../../actions/ticket_home_slider_actions";
import { getALLEvents, getUpcomingEvents } from "../../../actions/upcoming_events_actions";
import Events from "../../Event/Events";
import HomeSlider from "../../Home/HomeSlider/HomeSlider";
import Metatag from "../../Metatag/Metatag";
import News from "../../News/News";
import Profile from "../../Profile/Profile";
import Title from "../../Shared/Title/Title";
import HireArtists from "./HireArtists";
import "./Home.css";
import Subscribe from "./Subscribe";

export default function Home(props) {
    const { info } = props;

    const artist_settings = {
        dots: false,
        infinite: true,
        speed: 1000,
        slidesToShow: 3,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: true,
        adaptiveHeight: true,
        arrows: true,
        pauseOnHover: true,
        swipeToSlide: true,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 2,
                },
            },
            {
                breakpoint: 600,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    arrows: false,
                },
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    arrows: false,
                },
            },
        ],
    };

    const dispatch = useDispatch();
    const sliderList = useSelector((state) => state.sliderList);
    const { slider } = sliderList;
    useEffect(() => {
        dispatch(listSliders());
    }, [dispatch]);


    const upcomingEvents = useSelector((state) => state.getUpcomingEvents);
    const { upcoming } = upcomingEvents;
    useEffect(() => {
        dispatch(getUpcomingEvents());
    }, [dispatch]);


    const allEvents_data = useSelector((state) => state.getAllEvents);
    const { allevents } = allEvents_data;
    useEffect(() => {
        dispatch(getALLEvents());
    }, [dispatch]);

    const artists_data = useSelector((state) => state.getartist);
    const { artists } = artists_data;
    useEffect(() => {
        dispatch(artistAction());
    }, [dispatch]);


    return (
        <>
            <Metatag metatag_name={"home"} info={info} />
            {slider && (
            <HomeSlider slider={slider}/>
            )}

            <section className="home__section">
                <div className="home__section pt-3">
                    <Title
                        title="HIRE AN ARTIST "
                        link="/artists"
                        titleNumber={1}
                    />
                    <HireArtists artists={artists} />
                </div>
                {upcoming && (
                    <div className="home__section pt-3">
                        <Title
                            title="UP COMING EVENTS"
                            link="/upcoming-events"
                            titleNumber={1}
                        />
                        <Events upcoming={upcoming} />
                    </div>
                )}
                {allevents && (
                    <div className="pt-3">
                        <Title title="ALL EVENTS" link="/top-events" titleNumber={1} />
                        <Events upcoming={allevents} />
                    </div>
                )}

                {/* ************ News ************ */}
                <div className="events__section pt-3">
                    <Title title="RECENT NEWS" titleNumber={1} link="/blogs" />
                    <News />
                </div>
                {/* ************ Profile and News Letter ************ */}
                <div className="profile__section pt-3">
                    <Title title="Top 10 Artist in Bangladesh" />
                    <div className="container">
                        <div className="row g-3">
                            <div className="col-lg-8">
                                {artists && (
                                    <div className="row">
                                        <Slider {...artist_settings}>
                                            {artists.map((artists) => (
                                                <Profile key={artists.artist_id} artists={artists} />
                                            ))}
                                        </Slider>
                                    </div>
                                )}
                            </div>
                            <div className="col-lg-4">
                                <Subscribe />
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
