import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { listInfo } from "../actions/ticket_info_actions";
import Footer from "./Shared/Footer/Footer";
import Header from "./Shared/Header/Header";

const Layout = ({ children }) => {
    const dispatch = useDispatch();
    const getInfo = useSelector(state => state.getinfo);
    const { loading, error, info } = getInfo;
    useEffect(() => {
        dispatch(listInfo());
    }, [dispatch]);
    return (
        <>
            <Header loading={loading} error={error} info={info} />
            {children}
            <Footer loading={loading} error={error} info={info} />
        </>
    )
}

export default Layout