import {
    Accordion,
    AccordionItem, AccordionItemButton, AccordionItemHeading, AccordionItemPanel
} from 'react-accessible-accordion';
import 'react-accessible-accordion/dist/fancy-example.css';
import Title from '../../Shared/Title/Title';


const FAQ = () => {
    const items = [
        {
            id: 1,
            header: 'What harsh truths do you prefer to ignore?',
            content: 'In pariatur excepteur ut do aliquip qui mollit aliqua exercitation excepteur consequat reprehenderit nostrud laborum voluptate veniam non dolore dolore aliqua incididunt amet nisi minim cillum elit.',
        },
        {
            id: 2,
            header: 'Is free will real or just an illusion?',
            content: 'Dolor esse proident nisi minim nisi aute nulla sed proident magna id eiusmod consectetur laborum aliqua minim excepteur sunt anim anim esse aliquip et ea enim proident veniam veniam quis adipisicing nulla amet id commodo.'
        }
    ]
    return (
        <div className=" add-margin py-5">
            <Title title="Faq" />
            <div className="container">
                <Accordion allowZeroExpanded>
                    {items.map((item) => (
                        <AccordionItem key={item.uuid}>
                            <AccordionItemHeading>
                                <AccordionItemButton>
                                    {item.header}
                                </AccordionItemButton>
                            </AccordionItemHeading>
                            <AccordionItemPanel>
                                {item.content}
                            </AccordionItemPanel>
                        </AccordionItem>
                    ))}
                </Accordion>
            </div>
        </div >
    )
}

export default FAQ