const NameModal = () => {
    return (
        <div
            className="modal fade"
            id="nameModal"
            data-bs-backdrop="static"
            data-bs-keyboard="false"
            tabindex="-1"
            aria-labelledby="nameModal"
            aria-hidden="true"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 id="nameModal">Edit name question</h5>
                        <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div className="modal-body">
                        <p>
                            The 'Name' question is compulsory and can not be
                            removed from the form. Below you can edit the label
                            for this question.
                        </p>
                        <div className="mb-3">
                            <label for="name" className="form-label">
                                Label *
                            </label>
                            <input
                                type="name"
                                className="form-control"
                                id="name"
                            />
                        </div>
                    </div>
                    <div className="modal-footer">
                        <button
                            type="button"
                            id="cancel"
                            data-bs-dismiss="modal"
                        >
                            Close
                        </button>
                        <button type="button" id="submit">
                            Save Custom Question
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default NameModal;
