import { useEffect, useState } from 'react';
import { AiFillInstagram, AiFillTwitterCircle } from 'react-icons/ai';
import { FaEnvelope, FaFacebook, FaPhoneAlt, FaYoutube } from 'react-icons/fa';
import Input from '../../Input/Input';
import Select from '../../Input/Select';
import './Artist.scss';

const ArtistForm = ({ setIsSuccess, isSuccess }) => {
    const [counties, setCounties] = useState([]);
    const artistCategory = [
        { id: 1, value: 'singer', label: 'Anchor/Emcee' },
        { id: 2, value: 'singer', label: 'Singer' },
        { id: 3, value: 'singer', label: 'Comedian/Standup' },
        { id: 4, value: 'singer', label: 'Musician/Instrumentalist' },
    ];
    const interest = [
        { id: 1, value: 'online', label: 'Online' },
        { id: 2, value: 'offline', label: 'Offline' },
    ];


    useEffect(() => {
        fetch('https://restcountries.com/v2/all')
            .then(res => res.json())
            .then((data) => {
                const formatCounties = data.map((item, index) => {
                    return {
                        id: index,
                        value: item?.alpha2Code,
                        label: item.name
                    }
                });

                setCounties(formatCounties);
            })
    }, []);
    console.log(counties);


    const submitHandler = (event) => {
        event.preventDefault();
        setIsSuccess(!isSuccess);
    };

    return (
        <div className="contact__form__wrapper">
            <form className="contact__form" onSubmit={submitHandler}>
                <div className="row">
                    <div className="col-md-6">
                        <Input
                            label="firstName"
                            labelTitle="First Name"
                            required
                        />
                    </div>
                    <div className="col-md-6">
                        <Input
                            label="lastName"
                            labelTitle="Last Name"
                            required
                        />
                    </div>
                    <div className="col-md-6">
                        <Select
                            label="artistCategory"
                            labelTitle="Artist Category"
                            options={artistCategory}
                            required
                        />
                    </div>
                    <div className="col-md-6">
                        <Select
                            label="interest"
                            labelTitle="Interest"
                            options={interest}
                            required
                        />
                    </div>
                    <div className="col-md-6">
                        <Input
                            label="streetAdd"
                            labelTitle="Street Address"
                            required
                        />
                    </div>
                    <div className="col-md-6">
                        <Select
                            label="country"
                            labelTitle="Country"
                            options={counties}
                            required
                        />
                    </div>
                    <div className="col-12">
                        <label
                            htmlFor="message"
                            className="custom__form__label"
                        >
                            Bio
                        </label>
                        <textarea
                            name=""
                            id="message"
                            rows="6"
                            className="custom__form__input"
                            required
                        />

                    </div>
                    <div className="col-12 mb-3">
                        <label className="custom__form__label mt-3">
                            Social Links (optional)
                        </label>
                        <div className="row ">
                            <div className="col-md-6 mt-2">
                                <div className="row g-1">
                                    <label
                                        htmlFor="facebook"
                                        className="col-2"
                                    >
                                        <FaFacebook className="fs-4 mt-1" />
                                    </label>
                                    <div className="col-10">
                                        <input
                                            id="facebook"
                                            type="text"
                                            className="custom__form__input"
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 mt-2">
                                <div className="row g-1">
                                    <label
                                        htmlFor="instagram"
                                        className="col-2"
                                    >
                                        <AiFillInstagram className="fs-3 mt-1" />
                                    </label>
                                    <div className="col-10">
                                        <input
                                            id="instagram"
                                            type="text"
                                            className="custom__form__input"
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 mt-2">
                                <div className="row g-1">
                                    <label
                                        htmlFor="twitter"
                                        className="col-2"
                                    >
                                        <AiFillTwitterCircle className="fs-3 mt-1" />
                                    </label>
                                    <div className="col-10">
                                        <input
                                            id="twitter"
                                            type="text"
                                            className="custom__form__input"
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 mt-2">
                                <div className="row g-1">
                                    <label
                                        htmlFor="youtube"
                                        className="col-2"
                                    >
                                        <FaYoutube className="fs-3 mt-1" />
                                    </label>
                                    <div className="col-10">
                                        <input
                                            id="youtube"
                                            type="text"
                                            className="custom__form__input"
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-12 mb-3">
                        <label
                            htmlFor=""
                            className="custom__form__label"
                        >
                            Contact{' '}
                        </label>
                        <div className="row ">
                            <div className="col-md-6 mt-2">
                                <div className="row g-1">
                                    <label
                                        htmlFor="phone"
                                        className="col-2"
                                    >
                                        <FaPhoneAlt className="fs-5 mt-2" />
                                    </label>
                                    <div className="col-10">
                                        <input
                                            id="phone"
                                            type="text"
                                            className="custom__form__input"
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6 mt-2">
                                <div className="row g-1">
                                    <label
                                        htmlFor="mail"
                                        className="col-2"
                                    >
                                        <FaEnvelope className="fs-3" />{' '}
                                    </label>
                                    <div className="col-10">
                                        <input
                                            id="mail"
                                            type="email"
                                            className="custom__form__input"
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 mb-3">
                        <label className="custom__form__label">
                            Profile Photo <small>(320*320)</small>{' '}
                        </label>
                        <input class="form-control" type="file" />
                    </div>
                    <div className="col-md-6 mb-3">
                        <label className="custom__form__label">
                            Cover Photos <small>(320*320)</small>{' '}
                        </label>
                        <input class="form-control" type="file" multiple />
                    </div>
                </div>

                <div className="contact__submit text-center pt-5">
                    <button type="submit" className="custom__submit__btn">
                        Submit
                    </button>
                </div>
            </form>
        </div>
    );
};

export default ArtistForm;
