import {BLOG_LIST_REQUEST,BLOG_LIST_SUCCESS,BLOG_LIST_FAIL, BLOG_LIST_LIMITED_REQUEST, BLOG_LIST_LIMITED_SUCCESS, BLOG_LIST_LIMITED_FAIL,BLOG_DETAILS_REQUEST,BLOG_DETAILS_SUCCESS,BLOG_DETAILS_FAIL} from '../constants/ticket_blogs_constants';

export const allBlogsReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case BLOG_LIST_REQUEST:
            return {loading:true};

            case BLOG_LIST_SUCCESS:
                return {loading:false, all_blogs: action.payload};

                case BLOG_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}

