import { AiOutlineAppstore, AiOutlineCreditCard } from 'react-icons/ai';
import { BiBookContent } from 'react-icons/bi';
import { FaDonate, FaQuestionCircle, FaTimes } from 'react-icons/fa';
import { GoSearch } from 'react-icons/go';
import { HiOutlineTicket } from 'react-icons/hi';
import { MdContactPhone, MdSecurity } from 'react-icons/md';
import { RiFileList2Line, RiGlobalLine, RiQuestionnaireFill, RiVipCrown2Line } from 'react-icons/ri';
import { useHistory } from 'react-router';
import ReactSidebar from 'react-sidebar';
import logo from '../../../../assets/images/chill-hall.png';
import './Sidebar.scss';

const sidebarLinks = [
    { id: 1, href: '/host-show', title: 'Host Show', Icon: AiOutlineAppstore },
    { id: 2, href: '/buy-ticket', title: 'Buy Ticket', Icon: HiOutlineTicket },
    { id: 3, href: '/create-event', title: 'Create Event', Icon: RiFileList2Line },
    { id: 4, href: '/find-token', title: 'Find Token', Icon: GoSearch },
    { id: 6, href: '/', title: 'Language', Icon: RiGlobalLine },
    { id: 7, href: '/package', title: 'Package', Icon: RiVipCrown2Line },
    { id: 8, href: '/investor-relations', title: ' Investor Relationship', Icon: FaDonate },
    { id: 9, href: '/voucher-card', title: ' Voucher Card', Icon: AiOutlineCreditCard },
    { id: 10, href: '/story', title: 'Submit Your Content', Icon: BiBookContent },
    { id: 11, href: '/about', title: 'About Us', Icon: RiQuestionnaireFill },
    { id: 12, href: '/contact', title: 'Contact Us', Icon: MdContactPhone },
    { id: 13, href: '/privacy-policy', title: 'Privacy Policy', Icon: MdSecurity },
    { id: 14, href: '/faq', title: 'FAQ', Icon: FaQuestionCircle },
]


const Sidebar = ({ sidebarOpen, setSidebarOpen, children }) => {
    const history = useHistory();

    const onSetSidebarOpen = (open) => {
        setSidebarOpen(open);
        console.log(open)
    };
    return (
        <ReactSidebar
            sidebar={
                <div className="sidebar-wrapper">
                    <div className="sidebar-header">
                        <img
                            className="navbar-brand-icon img-fluid"
                            src={logo}
                            style={{ width: '55px' }}
                            alt="ChillHall"
                        />
                        <button onClick={() => setSidebarOpen(false)} type="button" className="btn times-icon">
                            <FaTimes />
                        </button>
                    </div>
                    <div className="side-menu">
                        {sidebarLinks.map(Item => (
                            <div key={Item.id} className="side-menu-item" onClick={() => { setSidebarOpen(false); history.push(`${Item.href}`) }} >
                                <Item.Icon />
                                {Item.title}
                            </div>
                        ))}
                    </div>
                    <div className="copy-right">
                        <p className="ps-4 mt-3">© chillhall.com 2021</p>
                    </div>
                </div>
            }
            open={sidebarOpen}
            onSetOpen={onSetSidebarOpen}
            styles={{
                sidebar: { background: 'red', width: '85%', zIndex: 2000, position: 'fixed', overflowY: 'hidden' },
                overlay: { zIndex: 222 },
            }}
        >
            {children}
        </ReactSidebar>
    )
}

export default Sidebar