import {EVENT_REQUEST_REQUEST,EVENT_REQUEST_SUCCESS,EVENT_REQUEST_FAIL} from '../constants/event_request';

import Axios from 'axios';
import instance from '../connection.js'

export const eventRequestActions = () => async(dispactch)=>{
    dispactch({
        type:EVENT_REQUEST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/event-requests');
        dispactch({type: EVENT_REQUEST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:EVENT_REQUEST_FAIL,payload: error.message});
    }
}
