import {LIKE_REQUEST_REQUEST,LIKE_REQUEST_SUCCESS,LIKE_REQUEST_FAIL} from '../constants/like_constant';

import Axios from 'axios';
import instance from '../connection.js'

export const likeartistaction = (
    slug) => async(dispactch)=>{
    dispactch({
        type:LIKE_REQUEST_REQUEST
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/like-artist',{slug});
        dispactch({type: LIKE_REQUEST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:LIKE_REQUEST_FAIL,payload: error.message});
    }
}
