import {INFO_LIST_REQUEST,INFO_LIST_SUCCESS,INFO_LIST_FAIL} from '../constants/ticket_infoConstants';
import Axios from 'axios';
import instance from '../connection.js'

export const listInfo = () => async(dispactch)=>{
    dispactch({
        type:INFO_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/getinfo');
        dispactch({type: INFO_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:INFO_LIST_FAIL,payload: error.message});
    }
}
