import {HOW_TO_HOST_LIST_REQUEST,HOW_TO_HOST_LIST_SUCCESS,HOW_TO_HOST_LIST_FAIL,HOW_TO_BUY_TICKET_LIST_REQUEST, HOW_TO_BUY_TICKET_FAIL, HOW_TO_BUY_TICKET_SUCCESS} from '../constants/How_to_host_your_show_constants';

export const howToHostYourShowListReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case HOW_TO_HOST_LIST_REQUEST:
            return {loading:true};

            case HOW_TO_HOST_LIST_SUCCESS:
                return {loading:false, how_to_host: action.payload};

                case HOW_TO_HOST_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}

export const getHowtobuyTicketReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case HOW_TO_BUY_TICKET_LIST_REQUEST:
            return {loading:true};

            case HOW_TO_BUY_TICKET_SUCCESS:
                return {loading:false, how_to_host: action.payload};

                case HOW_TO_BUY_TICKET_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}