import { BiHome, GoThreeBars, MdOndemandVideo } from 'react-icons/all';
import { BsMusicNoteList } from 'react-icons/bs';
import { RiUserHeartLine } from 'react-icons/ri';
import { NavLink } from 'react-router-dom';
import './HeaderBottom.scss';

const HeaderBottom = ({ setSidebarOpen }) => {
    return (
        <div className="header-bottom  d-lg-none ">
            <div className="header-bottom-menu">
                <NavLink exact to="/" className="menu-item">
                    <BiHome className="menu-item-icon" />
                    <span>Home</span>
                </NavLink>
                <NavLink to="/top-events" className="menu-item">
                    <MdOndemandVideo className="menu-item-icon" />
                    <span>Events</span>
                </NavLink>
                <NavLink to="/artist-portfolio" className="menu-item">
                    <BsMusicNoteList className="menu-item-icon" />
                    <span>Create</span>
                </NavLink>
                <NavLink to="/artists" className="menu-item">
                    <RiUserHeartLine className="menu-item-icon" />
                    <span>Artists</span>
                </NavLink>

                <div className="menu-item" type="button" onClick={() => setSidebarOpen(true)} >
                    <GoThreeBars className="menu-item-icon" />
                    <span>Menu</span>
                </div>
            </div>
        </div>
    );
};

export default HeaderBottom;
