import ReactImageAppear from "react-image-appear";
import Metatag from "../../Metatag/Metatag";
import Title from "../../Shared/Title/Title";
import "./About.css";
import instance from '../../../connection.js'

const About = (props) => {
    const { info } = props;
    return (
        <div className="add-margin">
            <Metatag metatag_name={"about_us"} info={info} />

            <Title title="About Us" />
            <div className="container">
                {info ? (
                    <div className="row">
                        <div className="col-lg-7">
                            <div className="about__content">
                                <div className="about__option">
                                    {info.map((info) => (
                                        <div key={info.info_id} dangerouslySetInnerHTML={{ __html: info.about_us }} />
                                    ))}
                                </div>
                            </div>
                        </div>
                        <div className="col-md-5">
                            <div className="about__img d-none d-lg-block">
                                {info.map((info) => (
                                    <ReactImageAppear key={info.info_id}
                                        src={instance+`/${info.about_image}`}
                                        alt={info.about_alt}
                                    />
                                ))}
                            </div>
                        </div>
                    </div>
                ) : (
                    <div></div>
                )}
            </div>
        </div>
    );
};

export default About;
