import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getALLEvents } from "../../../actions/upcoming_events_actions";
import Title from "../../Shared/Title/Title";
import "./FindToken.css";


export default function EventContent(props) {
  const { info } = props;
  const dispatch = useDispatch();
  const allEvents_data = useSelector((state) => state.getAllEvents);
  const { loading_all, error_all, allevents } = allEvents_data;
  useEffect(() => {
    dispatch(getALLEvents());
  }, []);
  return (
    <div className="FindTokenWrapper add-margin">
      <Title title="FIND YOUR Ticket" />
      <div className="container">
        <div className="row">
          <div className="col-lg-6">
            <div className="find__token pb-4">
              {info ? (
                <div>
                  {info.map((info) => (
                    <div
                      key={info.info_id}
                      dangerouslySetInnerHTML={{
                        __html: info.find_my_token_text,
                      }}
                    />
                  ))}
                </div>
              ) : (
                <div></div>
              )}
            </div>
          </div>
          <div className="col-lg-6">
            <form className="find__token__form contact__form__wrapper">
              <div className="custom__form__group">
                <label htmlFor="" className="custom__form__label">
                  Select Your Event
                </label>
                {allevents ? (
                  <select name="" id="" className="custom__form__input">
                    <option value="">--Select--</option>
                    {allevents.map((allevents) => (
                      <option key={allevents.event_id} value={allevents.event_id}>{allevents.event_title}</option>
                    ))}
                  </select>
                ) : (
                  <div></div>
                )}
              </div>
              <div className="custom__form__group">
                <label htmlFor="" className="custom__form__label">
                  Give You Mail or Phone Number
                </label>
                <input
                  type="text"
                  className="custom__form__input"
                  placeholder="Example: (chillhall@gmail.com)"
                />
              </div>
              <div className="custom__form__group">
                <button type="submit" className="custom__submit__btn">
                  Submit
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
