import {EVENTS_DETAILS_REQUEST,EVENTS_DETAILS_SUCCESS,EVENTS_DETAILS_FAIL} from '../constants/event_details_constants';

import Axios from 'axios';
import instance from '../connection.js'

export const eventDetailsActions = (slug) => async(dispactch)=>{
    dispactch({
        type:EVENTS_DETAILS_REQUEST
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/event-details',{slug:slug});
        dispactch({type: EVENTS_DETAILS_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:EVENTS_DETAILS_FAIL,payload: error.message});
    }
}
