import { Link } from 'react-router-dom';
import bg from '../../assets/images/bg-black.png';

const AccessCode = () => {
    return (
        <div className="basic__information">
            <div className="basic__information__form">
                <div className="info__title">
                    <h4>Access Code</h4>
                </div>

                <div className="access__code">
                    <div className="pb-2">
                        <h3>
                            Welcome To
                            <span
                                className="ms-2"
                                style={{ color: 'var(--primary)' }}
                            >
                                ChillHall
                            </span>
                        </h3>
                        <p>
                            Lorem ipsum dolor sit amet consectetur adipisicing
                            elit. que?
                        </p>
                    </div>
                    <div className="input__group">
                        <input
                            className="input__end"
                            type="search"
                            placeholder="Search ..."
                        />
                    </div>
                    <div className="row g-3">
                        {Array(3)
                            .fill('item')
                            .map((item, index) => (
                                <div className="col-md-6 col-lg-4">
                                    <div className="content__center">
                                        <div className="bg__section">
                                            <img
                                                src={bg}
                                                alt=""
                                                className="bg__dark"
                                            />
                                            <div className="event__content">
                                                <div className="border-bottom pb-1">
                                                    <h5>Event Name</h5>
                                                    <h6>
                                                        Saturday, 18 July 2021,
                                                        10:00PM
                                                    </h6>
                                                </div>
                                                <div className="pt-2">
                                                    <h6>
                                                        Paste Your Access Code
                                                    </h6>
                                                    <h6 className="token__code">
                                                        7jkx1
                                                    </h6>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            ))}
                    </div>
                    <div className="pt-3">
                        <p className="forget__token">
                            Forget your access code ?
                            <Link className="text-danger" to="/">
                                {' '}
                                Click Here
                            </Link>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AccessCode;
