import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  getALLEvents
} from "../../../actions/upcoming_events_actions";
import EventItem from "../../Event/EventItem";
import Title from "../../Shared/Title/Title";
import "./TopEvents.scss";

const TopEvents = (props) => {
  const dispatch = useDispatch();
  const allEvents_data = useSelector((state) => state.getAllEvents);
  const { loading_all, error_all, allevents } = allEvents_data;
  useEffect(() => {
    dispatch(getALLEvents());
  }, []);
  const { events } = props;
  return (
    <div className="top__events add-margin">
      <Title title="All Events" titleNumber={3} />
      {allevents ? (
        <div className="container">
          <div className="row g-2 g-md-3 ">
            {allevents.map((allevents) => (
              <div className="col-6 col-sm-6 col-md-4 col-lg-3 remove-zoom" key={allevents.event_id}>
                <EventItem upcoming={allevents} />
              </div>
            ))}
          </div>
        </div>
      ) : (
        <div></div>
      )}
    </div>
  );
};

export default TopEvents;
