import Vimeo from '@u-wave/react-vimeo';
import "./Video.scss";

export default function Video(props) {
  const { event_details } = props;
  return (
    <div className="video-container">
      <div className="video-player_one">
        <Vimeo
          video={event_details.trailer_video}
          autoplay
          muted
          loop
          responsive
          controls
          className="video"
        />
        {/*<video
          src=""
          width="100%"
          controls
          autoPlay
          className="video"
        ></video>*/}
      </div>
    </div>
  );
}
