import React from 'react';
import image from '../../../assets/images/Voucher-Card.png';
import Title from '../../Shared/Title/Title';
import './VoucherCard.scss';

const VoucherCard = () => {


    return (
        <div className="add-margin">
            <Title title="VOUCHER CARD" />

            <div className="container">
                <div className="row  pt-3">
                    <div className="col-md-4">
                        <div className="voucher__card mb-4">
                            <h4 className="voucher__card__title">
                                BOISHAKHI VOUCHER
                            </h4>
                            <div className="voucher__card__img">
                                <img src={image} alt="" />
                                <div className="copy-voucher-code">
                                    <button className="btn btn-outline-danger ">Copy Voucher Code</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="voucher__card mb-4">
                            <h4 className="voucher__card__title">EID VOUCHER</h4>
                            <div className="voucher__card__img">
                                <img src={image} alt="" />
                                <div className="copy-voucher-code">
                                    <button className="btn btn-outline-danger ">Copy Voucher Code</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="voucher__card mb-4">
                            <h4 className="voucher__card__title">
                                NEW YEAR VOUCHER
                            </h4>
                            <div className="voucher__card__img">
                                <img src={image} alt="" />
                                <div className="copy-voucher-code">
                                    <button className="btn btn-outline-danger ">Copy Voucher Code</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-4">
                        <div className="voucher__card mb-4">
                            <h4 className="voucher__card__title">
                                CHILLHALL VOUCHER
                            </h4>
                            <div className="voucher__card__img">
                                <img src={image} alt="" />
                                <div className="copy-voucher-code">
                                    <button className="btn btn-outline-danger ">Copy Voucher Code</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default VoucherCard;
