import {UPCOMING_EVENTS_LIST_REQUEST,UPCOMING_EVENTS_LIST_SUCCESS,UPCOMING_EVENTS_LIST_FAIL,ALL_EVENTS_LIST_REQUEST,ALL_EVENTS_LIST_SUCCESS,ALL_EVENTS_LIST_FAIL} from '../constants/upcoming_events_constant';

import Axios from 'axios';
import instance from '../connection.js'

export const getUpcomingEvents = () => async(dispactch)=>{
    dispactch({
        type:UPCOMING_EVENTS_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/upcoming-events');
        dispactch({type: UPCOMING_EVENTS_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:UPCOMING_EVENTS_LIST_FAIL,payload: error.message});
    }
}

export const getALLEvents = () => async(dispactch)=>{
    dispactch({
        type:ALL_EVENTS_LIST_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/all-events');
        dispactch({type: ALL_EVENTS_LIST_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:ALL_EVENTS_LIST_FAIL,payload: error.message});
    }
}
