import { Accordion, AccordionItem, AccordionItemButton, AccordionItemHeading, AccordionItemPanel } from "react-accessible-accordion"
import { AiFillContacts } from "react-icons/ai"
import { FaUsers, FaUsersCog } from "react-icons/fa"
import { GiReceiveMoney } from "react-icons/gi"
import { MdSecurity } from "react-icons/md"
import { RiVipCrown2Fill } from "react-icons/ri"

const Benefits = () => {
    const items = [
        {
            id: 1,
            header: 'Are You a Professional Artist?',
            content: 'In pariatur excepteur ut do aliquip qui mollit aliqua exercitation excepteur consequat reprehenderit nostrud laborum voluptate veniam non dolore dolore aliqua incididunt amet nisi minim cillum elit.',
        },
        {
            id: 2,
            header: 'Why will you  create your Portfolio here?',
            content: 'Dolor esse proident nisi minim nisi aute nulla sed proident magna id eiusmod consectetur laborum aliqua minim excepteur sunt anim anim esse aliquip et ea enim proident veniam veniam quis adipisicing nulla amet id commodo.'
        },
        {
            id: 3,
            header: 'Do you want to contact with Our support team?',
            content: 'Dolor esse proident nisi minim nisi aute nulla sed proident magna id eiusmod consectetur laborum aliqua minim excepteur sunt anim anim esse aliquip et ea enim proident veniam veniam quis adipisicing nulla amet id commodo.'
        }
    ]
    return (
        <div className="mb-5">
            <h4 className="benefits-title">Benefits</h4>
            <div className="benefits">
                <div className="benefits-item">
                    <div className="benefits-icon">
                        <AiFillContacts />
                    </div>
                    <p>Own Portfolio</p>
                </div>
                <div className="benefits-item">
                    <div className="benefits-icon">
                        <FaUsers />
                    </div>
                    <p>Host Events</p>
                </div>
                <div className="benefits-item">
                    <div className="benefits-icon">
                        <GiReceiveMoney />
                    </div>
                    <p>Earn Money</p>
                </div>
            </div>
            <div className="benefits2 mt-4">
                <div className="benefits-item benefits-item2 ">
                    <div className="benefits-icon">
                        <FaUsersCog />
                    </div>
                    <p>Connecting Events</p>
                </div>
                <div className="benefits-item benefits-item2">
                    <div className="benefits-icon">
                        <MdSecurity />
                    </div>
                    <p>Secure Connection</p>
                </div>
                <div className="benefits-item benefits-item2">
                    <div className="benefits-icon">
                        <RiVipCrown2Fill />
                    </div>
                    <p>Premium Support</p>
                </div>
            </div>
            <div className="mt-4">
                <Accordion allowZeroExpanded>
                    {items.map((item) => (
                        <AccordionItem key={item.uuid}>
                            <AccordionItemHeading>
                                <AccordionItemButton>
                                    {item.header}
                                </AccordionItemButton>
                            </AccordionItemHeading>
                            <AccordionItemPanel>
                                {item.content}
                            </AccordionItemPanel>
                        </AccordionItem>
                    ))}
                </Accordion>
            </div>
        </div>
    )
}

export default Benefits