import { useState } from "react";
import { useLocation } from "react-router";
import Metatag from "../Metatag/Metatag";
import Title from '../Shared/Title/Title';
import './CreateEvent.scss';
import EventContent from './EventContent';
import OfflineForm from "./OfflineForm";
import EventForm from './VirtualForm';


function useQuery() {
    return new URLSearchParams(useLocation().search);
}

function CreateEvent(props) {
    const { info } = props;
    const [show, setShow] = useState("online");

    let query = useQuery();

    return (
        <div className="add-margin">
            <Metatag metatag_name={"create_event"} info={info} />
            <Title title="CREATE EVENT" />
            <div className="container">
                <div className="row g-4">
                    <div className="col-lg-6">
                        <EventContent info={info} />
                    </div>
                    <div className="col-lg-6">
                        <div className="show-select">
                            <h6 className={`show-item ${show === 'online' && 'active'} `} onClick={() => setShow('online')} >Virtual Show</h6>
                            <h6 className={`show-item ${show === 'offline' && 'active'} `} onClick={() => setShow('offline')}>Offline Show</h6>
                        </div>
                        {show === 'online' && <EventForm query={query.get("name")} />}
                        {show === 'offline' && <OfflineForm query={query.get("name")} />}

                    </div>
                </div>
            </div>
        </div>
    );
};


export default CreateEvent;