import { useState } from 'react';

const EmailModal = () => {
    const [showMail, setShowMail] = useState(false);

    return (
        <div
            className="modal fade"
            id="emailModal"
            data-bs-backdrop="static"
            data-bs-keyboard="false"
            tabindex="-1"
            aria-labelledby="emailModal"
            aria-hidden="true"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 id="emailModal">Edit name question</h5>
                        <button
                            type="button"
                            className="btn-close"
                            data-bs-dismiss="modal"
                            aria-label="Close"
                        ></button>
                    </div>
                    <div className="modal-body">
                        <p>
                            The 'Name' question is compulsory and can not be
                            removed from the form. Below you can edit the label
                            for this question.
                        </p>
                        <div className="mb-3">
                            <label for="email" className="form-label">
                                Label *
                            </label>
                            <input
                                type="text"
                                className="form-control"
                                id="email"
                                placeholder="Email"
                            />
                        </div>
                        <div className="mb-3">
                            <input
                                className="form-check-input"
                                type="checkbox"
                                id="mail_send"
                                onClick={() => setShowMail(!showMail)}
                            />
                            <label className="form-check-label" for="mail_send">
                                Customers have to enter their email twice to
                                avoid typos
                            </label>
                        </div>
                        {showMail && (
                            <div className="mb-3">
                                <label for="repeatEmail" className="form-label">
                                    Repeat email label *
                                </label>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="repeatEmail"
                                    placeholder="Repeat Email"
                                />
                            </div>
                        )}
                    </div>

                    <div className="modal-footer">
                        <button
                            type="button"
                            id="cancel"
                            data-bs-dismiss="modal"
                        >
                            Close
                        </button>
                        <button type="button" id="submit">
                            Save Question
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default EmailModal;
