import React, { Suspense } from 'react';
import { Route, Switch, withRouter } from 'react-router';
import AccessCode from './AccessCode';
import ArtistPortfolio from './ArtistPortfolio/ArtistPortfolio';
import ChangePassword from './ChangePassword';
import './Customer.scss';
import Dashboard from './Dashboard';
import ProfileRoutes from './ProfileRoutes';
import Reviews from './Reviews';
import Subscriptions from './Subscriptions';
import Tickets from './Tickets';
import Transaction from './Transaction';
import Wallet from './Wallet';

const Profile = ({ match }) => {
    return (
        <section className="profile__section add-margin">
            <div className="container py-4">
                <div className="row g-3">
                    <div className="col-md-3">
                        <ProfileRoutes />
                    </div>
                    <div className="col-md-9">
                        <div className="profile__routes__content shadow-sm">
                            <Switch>
                                <Route exact path={`${match.path}/`}>
                                    <Suspense fallback="Loading...">
                                        <Dashboard />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/subscriptions`}>
                                    <Suspense fallback="Loading...">
                                        <Subscriptions />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/tickets`}>
                                    <Suspense fallback="Loading...">
                                        <Tickets />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/wallet`}>
                                    <Suspense fallback="Loading...">
                                        <Wallet />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/transaction`}>
                                    <Suspense fallback="Loading...">
                                        <Transaction />
                                    </Suspense>
                                </Route>

                                <Route path={`${match.path}/reviews`}>
                                    <Suspense fallback="Loading...">
                                        <Reviews />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/access-code`}>
                                    <Suspense fallback="Loading...">
                                        <AccessCode />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/artist-portfolio`}>
                                    <Suspense fallback="Loading...">
                                        <ArtistPortfolio />
                                    </Suspense>
                                </Route>
                                <Route path={`${match.path}/change-password`}>
                                    <Suspense fallback="Loading...">
                                        <ChangePassword />
                                    </Suspense>
                                </Route>
                            </Switch>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default withRouter(Profile);
