import { AiOutlineForm } from 'react-icons/ai';
import {
    BsCardChecklist,
    BsChatSquareQuote,
    BsFillShieldLockFill,
} from 'react-icons/bs';
import { FaMoneyCheckAlt } from 'react-icons/fa';
import { FiSettings, FiTool } from 'react-icons/fi';
import { MdPayment } from 'react-icons/md';
import { RiFileList2Line } from 'react-icons/ri';
import { NavLink } from 'react-router-dom';

const Sidebar = () => {
    return (
        <div className="profile-menu shadow-sm">
            <div className="py-4">
                <NavLink
                    exact
                    to="/settings"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <FiTool />
                    </span>
                    <span className="profile-route">Basic Setting</span>
                </NavLink>

                <NavLink
                    to="/settings/event-form"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <BsCardChecklist />
                    </span>
                    <span className="profile-route">Event Form</span>
                </NavLink>

                <NavLink
                    to="/settings/event-request"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <BsChatSquareQuote />
                    </span>
                    <span className="profile-route">Event Request</span>
                </NavLink>
                <NavLink
                    to="/settings/info"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <FiSettings />
                    </span>
                    <span className="profile-route">Info Setup</span>
                </NavLink>

                <NavLink
                    to="/settings/checkout-form"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <AiOutlineForm />
                    </span>
                    <span className="profile-route">Checkout Form</span>
                </NavLink>

                <NavLink
                    to="/settings/order-confirm"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <RiFileList2Line />
                    </span>
                    <span className="profile-route">Order Confirm</span>
                </NavLink>

                <NavLink
                    to="/settings/privacy-policy"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <BsFillShieldLockFill />
                    </span>
                    <span className="profile-route">Privacy Policy</span>
                </NavLink>

                <NavLink
                    to="/settings/payment"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <MdPayment />
                    </span>
                    <span className="profile-route">Payment System</span>
                </NavLink>

                <NavLink
                    to="/settings/billing"
                    className="profile-menu-item"
                    activeClassName="profile-active"
                >
                    <span className="profile-icon">
                        <FaMoneyCheckAlt />
                    </span>
                    <span className="profile-route">Billing</span>
                </NavLink>
            </div>
        </div>
    );
};

export default Sidebar;
