import { useEffect, useState } from 'react';
import { IoIosArrowDown } from 'react-icons/all';
import { FaWhatsapp } from 'react-icons/fa';
import './DownloadApp.scss';

const Aside = () => {
    const [offset, setOffset] = useState(0);

    useEffect(() => {
        window.onscroll = () => {
            setOffset(window.pageYOffset);
        };
    }, []);

    const handleTop = () => {
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    return (
        <div id="aside" style={{ visibility: `${offset >= 300 ? 'visible' : 'hidden'}` }} >
            <div className="asideBar">
                <ul className="asideOptions">
                    <li className="qrCode list__item">
                        {/* <HiOutlineDeviceMobile className="icon" /> */}
                        <a href="https://api.whatsapp.com/send?phone=+8801628606201">
                            <FaWhatsapp className="icon" />
                        </a>
                        {/* <div className="app">
                            <div className="downBox">
                                <div className="info">
                                    Download App
                                </div>
                                <div className="img">
                                    <img
                                        src="https://s.staticbg.com/web/src/img/app_qrcode.png?v=037d5b2"
                                        alt=""
                                    />
                                </div>
                                <div className="text-center">
                                    <p>OR</p>
                                    <ul className="buttons">
                                        <li>
                                            <a href="/">
                                                <FaApple /> Apple
                                            </a>
                                        </li>
                                        <li>
                                            <a href="/">
                                                <AiFillAndroid /> Android
                                            </a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div> */}
                    </li>
                    <li
                        className="topArrow list__item"
                        style={{ cursor: 'pointer', }}
                        onClick={handleTop}
                    >
                        <IoIosArrowDown className="icon upArrow" />
                        <span className="tip">Go Top</span>
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default Aside;