import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Route, Switch } from 'react-router-dom';
import { listInfo } from '../actions/ticket_info_actions';
import CreateEvent from '../components/CreateEvent/CreateEvent';
import Auth from './Auth/Auth';
import BlogDetails from './Blogs/BlogDetails';
import Blogs from './Blogs/Blogs';
import Profile from './Customer/Profile';
import Layout from './Layout';
import About from './Pages/About/About';
import Artist from './Pages/Artist/Artist';
import Artists from './Pages/Artists/Artists';
import BuyTicket from './Pages/BuyTicket/BuyTicket';
import Contact from './Pages/Contact/Contact';
import EventDetails from './Pages/EventDetails/EventDetails';
import EventMain from './Pages/EventDetails/event_main';
import FAQ from './Pages/FAQ/FAQ';
import FindToken from './Pages/FindToken/FindToken';
import Home from './Pages/Home/Home';
import HostShow from './Pages/HostShow/HostShow';
import InventorRelations from './Pages/InventorRelations/InventorRelations';
import Package from './Pages/Package/Package';
import CancelPolicy from './Pages/Policy/CancelPolicy';
import PrivacyPolicy from './Pages/Policy/PrivacyPolicy';
import ReturnPolicy from './Pages/Policy/ReturnPolicy';
import TermAndCondition from './Pages/Policy/TermAndCondition';
import Search from './Pages/Search/Search';
import Story from './Pages/Story/Story';
import TopEvents from './Pages/TopEvents/TopEvents';
import UpcomingEvents from './Pages/TopEvents/UpcomingEvents';
import VoucherCard from './Pages/VoucherCard/VoucherCard';
import ProfileDetails from './Profile/ProfileDetails';
import Settings from './Settings/Settings';
import TicketSteps from './Tickets/TicketSteps';

import { ProtectedRoute } from "../protected_routes";

const Routes = () => {
    const dispatch = useDispatch();
    const getInfo = useSelector((state) => state.getinfo);
    const { loading, error, info } = getInfo;
    useEffect(() => {
        dispatch(listInfo());
    }, [dispatch]);

    return (
        <Layout>
            <Switch>
                <Route exact path="/">
                    <Home info={info} />
                </Route>

                <Route path="/host-show">
                    <HostShow info={info} />
                </Route>
                <Route path="/investor-relations">
                    <InventorRelations />
                </Route>

                <Route path="/voucher-card">
                    <VoucherCard />
                </Route>

                <Route exact path='/package'>
                    <Package />
                </Route>
                <Route exact path='/story'>
                    <Story />
                </Route>

                <Route path="/search">
                    <Search info={info} />
                </Route>

                <Route path="/buy-ticket/">
                    <BuyTicket info={info} />
                </Route>

                <Route path="/artist-portfolio">
                    <Artist />
                </Route>
                <Route path="/artists">
                    <Artists />
                </Route>

                <Route path="/find-token">
                    <FindToken info={info} />
                </Route>

                <Route path="/ticket/:slug">
                    <TicketSteps />
                </Route>

                <Route path="/profile-details/:slug">
                    <ProfileDetails />
                </Route>
                <Route path="/create-event">
                    <CreateEvent info={info} />
                </Route>

                <Route path="/event-details/:slug">
                    <EventDetails info={info} />
                </Route>

                <Route path="/EventMain/:slug">
                    <EventMain info={info} />
                </Route>

                <Route path="/top-events">
                    <TopEvents />
                </Route>

                <Route path="/upcoming-events">
                    <UpcomingEvents />
                </Route>

                <Route path="/blogs">
                    <Blogs />
                </Route>

                <Route path="/blog-details/:slug">
                    <BlogDetails info={info} />
                </Route>

                <Route path="/about">
                    <About loading={loading} error={error} info={info} />
                </Route>

                <Route path="/contact">
                    <Contact info={info} />
                </Route>

                <Route path="/privacy-policy">
                    <PrivacyPolicy
                        loading={loading}
                        error={error}
                        info={info}
                    />
                </Route>

                <Route path="/return-policy">
                    <ReturnPolicy loading={loading} error={error} info={info} />
                </Route>

                <Route path="/cancel-policy">
                    <CancelPolicy loading={loading} error={error} info={info} />
                </Route>
                <Route path="/term-and-condition">
                    <TermAndCondition loading={loading} error={error} info={info} />
                </Route>

                <Route path="/login">
                    <Auth />
                </Route>
                <Route exact path="/faq">
                    <FAQ />
                </Route>

                <Route path="/user-profile">
                    <Profile />
                </Route>

                <Route path="/settings">
                    <Settings />
                </Route>
            </Switch>
        </Layout>
    );
};

export default Routes;
