import ReactImageZoom from 'react-image-zoom';
import Icons from '../../Shared/Footer/Icons';

const EventDescription = (props) => {
    const { description } = props;
    const settings = { width: 350, zoomPosition: 'original' };
    return (
        <div className="container pt-3">
            <div className="row g-3">
                <div className="col-lg-4">
                    <div className="event__description__img text-center">
                        <ReactImageZoom
                            {...settings}
                            img={`/${description.image}`}
                        />
                        <div className="event-icons">
                            <Icons />
                        </div>
                    </div>
                </div>
                <div className="col-lg-8">
                    <div className="event__description box-shadow">
                        <div className="event__description__option">
                            <h5>Description:</h5>
                            <div dangerouslySetInnerHTML={{ __html: description.event_description }} />
                        </div>

                    </div>
                </div>
            </div>
        </div>
    );
};

export default EventDescription;
