import {META_LIST_REQUEST,META_LIST_SUCCESS,META_LIST_FAIL} from '../constants/meta_tag_constants';

export const metaListReducer = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case META_LIST_REQUEST:
            return {loading:true};

            case META_LIST_SUCCESS:
                return {loading:false, metatag: action.payload};

                case META_LIST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}