import {ARTISTS_REQUEST,ARTISTS_SUCCESS,ARTISTS_FAIL,ARTISTS_DETAILS_REQUEST,ARTISTS_DETAILS_SUCCESS,ARTISTS_DETAILS_FAIL} from '../constants/artist_constants';

export const artistReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case ARTISTS_REQUEST:
            return {loading:true};

            case ARTISTS_SUCCESS:
                return {loading:false, artists: action.payload};

                case ARTISTS_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}

export const artistDetailsReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case ARTISTS_DETAILS_REQUEST:
            return {loading:true};

            case ARTISTS_DETAILS_SUCCESS:
                return {loading:false, artists_details: action.payload};

                case ARTISTS_DETAILS_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}