import {ARTISTS_REQUEST,ARTISTS_SUCCESS,ARTISTS_FAIL,ARTISTS_DETAILS_REQUEST,ARTISTS_DETAILS_SUCCESS,ARTISTS_DETAILS_FAIL} from '../constants/artist_constants';

import Axios from 'axios';
import instance from '../connection.js'

export const artistAction = () => async(dispactch)=>{
    dispactch({
        type:ARTISTS_REQUEST
    });
    try{
        const {data} = await Axios.get(instance+'/ticket/home/all-artists');
        dispactch({type: ARTISTS_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:ARTISTS_FAIL,payload: error.message});
    }
}

export const artistDetailsAction = (slug) => async(dispactch)=>{
    dispactch({
        type:ARTISTS_DETAILS_REQUEST
    });
    try{
        const {data} = await Axios.post(instance+'/ticket/home/artists-details',{slug:slug});
        dispactch({type: ARTISTS_DETAILS_SUCCESS,payload:data});
    }catch(error){
        dispactch({type:ARTISTS_DETAILS_FAIL,payload: error.message});
    }
}
