import React, { useState } from 'react';
import InformationForm from './InformationForm';
import Package from './Package';

const Dashboard = () => {
    const [info, setInfo] = useState(true);

    const setHandler = (input) => {
        setInfo(input);
    };
    return (
        <div className="basic__information add-margin">
            <div className="basic__information__form">
                <div className="info__title">
                    <h4>Dashboard</h4>
                </div>
                <Package />
                {info ? (
                    <div className="information">
                        <div className="information__body">
                            <div className="information__body_item">
                                <p>First Name: </p>
                                <p>Sujon</p>
                            </div>
                            <div className="information__body_item">
                                <p>Last Name: </p>
                                <p> Hossain </p>
                            </div>
                            <div className="information__body_item">
                                <p>Contact Number: </p>
                                <p> 019 00 00 00 00 </p>
                            </div>
                            <div className="information__body_item">
                                <p>Email Address: </p>
                                <p> example@example.com </p>
                            </div>
                            <div className="information__body_item">
                                <p>Gender: </p>
                                <p> Male </p>
                            </div>
                            <div className="information__body_item">
                                <p>Date of Birth: </p>
                                <p>21 Feb 2202</p>
                            </div>
                            <div className="information__body_item">
                                <p>Member Since: </p>
                                <p>21 Feb 2202</p>
                            </div>
                        </div>
                        <div className="text-start pt-3 pe-2">
                            <button
                                type="button"
                                className="cancel__btn"
                                onClick={() => setHandler(false)}
                            >
                                Edit
                            </button>
                        </div>
                    </div>
                ) : (
                    <InformationForm setHandler={setHandler} />
                )}

                <div className="current__package">
                    <div className="package__img">
                        <img src="" alt="" />
                    </div>
                    <h4>Current Package: Monthly Package</h4>
                    <h5>Price: $25</h5>
                    <div className="mt-3">
                        <a
                            href="/"
                            className="custom__submit__btn text-white py-2"
                        >
                            UPGRADE PACKAGE
                        </a>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Dashboard;
