import { useState } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import Routes from './Routes';
import ScrollToTop from './ScrollToTop';
import DarkMode from './Shared/DarkMode/DarkMode';
import DownloadApp from './Shared/DownloadApp/DownloadApp';
import HeaderBottom from './Shared/Header/HeaderBottom/HeaderBottom';
import Sidebar from './Shared/Header/Sidebar/Sidebar';

function App() {
    const [sidebarOpen, setSidebarOpen] = useState(false);
    return (
        <Router>
            <ScrollToTop />
            <DarkMode />
            <DownloadApp />
            {sidebarOpen && <Sidebar sidebarOpen={sidebarOpen} setSidebarOpen={setSidebarOpen} />}
            <HeaderBottom setSidebarOpen={setSidebarOpen} />
            <Routes />
        </Router>
    );
}

export default App;
