import {LIKE_REQUEST_REQUEST,LIKE_REQUEST_SUCCESS,LIKE_REQUEST_FAIL} from '../constants/like_constant';

export const likeRequestReduces = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case LIKE_REQUEST_REQUEST:
            return {loading:true};

            case LIKE_REQUEST_SUCCESS:
                return {loading:false, like_request: action.payload};

                case LIKE_REQUEST_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}
