import Title from '../../Shared/Title/Title';
import './Package.scss';
import PackageItem from "./PackageItem";

const Package = () => {
    return (
        <div className="add-margin package-wrapper">
            <Title title="Packages" />
            <div className="container">
                <div className="row g-sm-3 g-4">
                    <div className="col-md-4">
                        <PackageItem />
                    </div>
                    <div className="col-md-4">
                        <PackageItem />
                    </div>
                    <div className="col-md-4">
                        <PackageItem />
                    </div>
                    <div className="col-md-4">
                        <PackageItem />
                    </div>
                    <div className="col-md-4">
                        <PackageItem />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Package