const UpdatePortfolio = ({ setShow, show }) => {
    return (
        <form>
            <div className="input__group">
                <label htmlFor="">First Name</label>
                <input type="text" name="" id="" placeholder="First Name" />
            </div>
            <div className="input__group">
                <label htmlFor="">Last Name</label>
                <input type="text" name="" id="" placeholder="Last Name" />
            </div>
            <div className="input__group">
                <label htmlFor="">Email Address</label>
                <input type="text" name="" id="" placeholder="Email Address" />
            </div>
            <div className="input__group">
                <label htmlFor="">Address</label>
                <input type="text" name="" id="" placeholder="Email Address" />
            </div>
            <div className="input__group">
                <label htmlFor="">Contact Number</label>
                <input type="text" name="" id="" placeholder="Email Address" />
            </div>

            <div className="input__group">
                <label htmlFor="">Artist Category</label>
                <div className="input__select__group">
                    <select
                        name=""
                        id=""
                        className="input__select appearance-none"
                    >
                        <option value="male">Singer</option>
                        <option value="other">Other</option>
                    </select>
                </div>
            </div>
            <div className="input__group">
                <label htmlFor="">Interest</label>
                <div className="input__select__group">
                    <select
                        name=""
                        id=""
                        className="input__select appearance-none"
                    >
                        <option value="female">Online</option>
                        <option value="other">Offline</option>
                    </select>
                </div>
            </div>
            <div className="input__group">
                <label htmlFor="">Social Media</label>
                <input type="text" name="" id="" placeholder="facebook username" />
                <input type="text" name="" id="" placeholder="instagram username" />
                <input type="text" name="" id="" placeholder="twitter username" />
                <input type="text" name="" id="" placeholder="youtube username" />
            </div>

            <div className="input__group">
                <label htmlFor="">Bio:</label>
                <textarea className="form-control" type="text" name="" id="" placeholder="Occupation" />
            </div>
            <div className="input__group">
                <label htmlFor="">Profile Image:</label>
                <input type="file" name="" id="" />
            </div>
            <div className="input__group">
                <label htmlFor="">Cover Images (max 10) :</label>
                <input type="file" name="" id="" multiple />
            </div>
            <div className="input__group">
                <button
                    type="button"
                    className="cancel__btn"
                    onClick={() => setShow(!show)}
                >
                    Cancel
                </button>
                <button
                    type="submit"
                    className="cancel__btn"
                    style={{ background: 'var(--secondary)' }}
                >
                    Submit
                </button>
            </div>
        </form>
    );
};

export default UpdatePortfolio;
