import {EVENTS_DETAILS_REQUEST,EVENTS_DETAILS_SUCCESS,EVENTS_DETAILS_FAIL} from '../constants/event_details_constants';

export const eventDetailsReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case EVENTS_DETAILS_REQUEST:
            return {loading:true};

            case EVENTS_DETAILS_SUCCESS:
                return {loading:false, event_details: action.payload};

                case EVENTS_DETAILS_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}