import {CREATE_EVENET_REQUEST,CREATE_EVENET_SUCCESS,CREATE_EVENET_FAIL,CREATE_EVENET_REQUEST_OFFLINE,CREATE_EVENET_SUCCESS_OFFLINE,CREATE_EVENET_FAIL_OFFLINE} from '../constants/create_event_constants';

export const createEventReducers = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case CREATE_EVENET_REQUEST:
            return {loading:true};

            case CREATE_EVENET_SUCCESS:
                return {loading:false, create_event: action.payload};

                case CREATE_EVENET_FAIL:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}


export const createEventReducersOffline = (state = {loading:true,products: []},action) =>{
    switch(action.type){
        case CREATE_EVENET_REQUEST_OFFLINE:
            return {loading:true};

            case CREATE_EVENET_SUCCESS_OFFLINE:
                return {loading:false, create_event: action.payload};

                case CREATE_EVENET_FAIL_OFFLINE:
                    return {loading:false, error: action.payload};
            default:
                return state;
    }
}